

import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';

const mapStateToProps = (state) => {
  return {
    gallery: state.gallerySlide.currentGallery,
    gridGallery: state.gallerySlide.currentGallery.grid,
    screenSize: state.card.screenSize,
    imageListDefult: state.gallerySlide.imageListDefult

  };
}

const mapDispatchToProps = (dispatch) => ({
  setCols: (num) => dispatch(actions.setCols(num)),
})

export default connect(mapStateToProps, mapDispatchToProps)(function ImageGridList(props) {
  const { gallery, gridGallery, screenSize, showState, preview, imageListDefult } = props;

  let height = 0, w = parseInt(window.innerWidth);
  if (!showState || preview) {
    if (screenSize === 1)
      height = gridGallery.cellHeightDesktop;
    else
      height = gridGallery.cellHeight;
  }
  else {
    if (w >= 600)
      height = gridGallery.cellHeightDesktop;
    else
      height = gridGallery.cellHeight;
  }

  return (
    <div style={gridGallery.useStyles.root}>
      {gallery.galleries.length !== 0 ?
        <GridList style={screenSize === 0 ? gridGallery.useStyles.gridList : ""} cols={gridGallery.cols} cellHeight={height}>
          {gallery.galleries.map((tile) => {
            const ref = React.createRef();

            const handleClick = () =>
              ref.current.scrollIntoView({
                behavior: 'smooth',
                block: 'start',
              });

            return (
              <GridListTile key={tile.backgroundImage} cols={tile.featured ? 2 : 1} rows={tile.featured ? 2 : 1}>
                <img
                  src={tile.backgroundImage}
                  alt={tile.title}
                  style={{ border: "1px solid " + gallery.borderColor }}
                  ref={"20px"}
                  onClick={handleClick}
                />

              </GridListTile>

            );


          })}
        </GridList> : <>
          <GridList style={screenSize === 0 ? gridGallery.useStyles.gridList : ""} cols={gridGallery.cols} cellHeight={height}>
            {imageListDefult.map((tile, i) => (
              <GridListTile key={i}>
                <img src={tile.backgroundImage} style={{ border: "1px solid " + gallery.borderColor }}
                />
              </GridListTile>
            ))}
          </GridList>
        </>
      }


    </div>
  );
});