import React, { Component } from "react";

import '../../Css/Nav.css'

export default class IconShapStyle extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };

    }

    render() {
        return (
            <div className="container-fluid mt-2 mb-2">
                <div className="row">
                    <label className="title">Icon Shape</label>
                </div>
                <div className="row d-flex justify-content-around">
                    <div className="col " onClick={() => this.props.changShapRadius("50%")}>
                        <div className="row" style={{
                            width: "30px",
                            height: "30px",
                            background: "#F5F5FA",
                            borderRadius: "50%"
                        }}></div>
                        <label className="row title">Round</label>

                    </div>


                    <div className="col " onClick={() =>  this.props.changShapRadius("0%")}>
                        <div className="row" style={{
                            width: "30px",
                            height: "30px",
                            background: "#B8B8B8",
                            borderRadius: "0%",
                        }}></div>
                        <label className="row title">Square</label>

                    </div>

                    <div className="col " onClick={() => this.props.changShapRadius("20%")}>
                        <div className="row " style={{
                            width: "30px",
                            height: "30px",
                            background: "#B8B8B8",
                            borderRadius: "20%"
                        }}></div>
                        <label className="row title">Radius</label>

                    </div>
                </div>

            </div >
        );
    }
}
