import React from 'react'
import Slider from '@material-ui/core/Slider';
import Cropper from 'react-easy-crop'
import RotateRightSharpIcon from '@material-ui/icons/RotateRightSharp';
import { withStyles } from '@material-ui/core/styles'
import { styles } from './styles'
import '../Css/UploadFile.css'

const CropperComponent = (props) => {
    const {
        classes,
        img,
        crop,
        rotation,
        zoom,
        aspect,
        onCropChange,
        onRotationChange,
        onCropComplete,
        onZoomChange,
        cropShape } = props;

    return (
        <>
            <div>
                {/* //mobile */}
                <div className={classes.cropContainer}>
                    <Cropper
                        image={img}
                        crop={crop}
                        rotation={rotation}
                        zoom={zoom}
                        aspect={aspect} //first is width
                        onCropChange={onCropChange}
                        onRotationChange={onRotationChange}
                        onCropComplete={onCropComplete}
                        onZoomChange={onZoomChange}
                        cropShape={cropShape}
                        style={
                            {
                                containerStyle:
                                {
                                    background: img !== "" ? "#EBEAEA" : ""
                                }
                            }
                        }
                    />
                </div>
                {img !== "" ?
                    <div className={classes.controls}>
                        <div className={classes.sliderContainer}>
                            <label
                                className={classes.sliderLabel}>
                                Scale
                        </label>
                            <Slider
                                value={zoom}
                                min={1}
                                max={3}
                                step={0.1}
                                aria-labelledby="Zoom"
                                onChange={(e, zoom) => onZoomChange(zoom)}
                            />
                        </div>
                        <div className={classes.sliderContainer}>
                            <label
                                className={classes.sliderLabel}>
                                <RotateRightSharpIcon></RotateRightSharpIcon>
                            Rotation
                        </label>
                            <input
                                type="number"
                                min={0}
                                max={360}
                                defaultValue={0}
                                className={classes.InputNumber}
                                onChange={(e) => onRotationChange(e.target.value)}
                            >
                            </input>
                        </div>

                    </div>
                    : ""}

            </div>
        </>
    )
}

export default withStyles(styles)(CropperComponent)





