import React from 'react'
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';



const mapStateToProps = (state) => {
  return {
    currentVideo: state.video.currentVideo,
    screenSize: state.card.screenSize,

  };
}

const mapDispatchToProps = (dispatch) => ({


})

export default connect(mapStateToProps, mapDispatchToProps)(function VideoCard(props) {
  const { currentVideo, screenSize } = props;
  const url = currentVideo.videoCard.url



  return (
    <div className='player-wrapper'>
      {url ?
        <iframe
          width='100%'
          height={screenSize === 1 ? '300' : '100%'} src={`https://www.youtube.com/embed/${url.split('=')[1]}`}  >
        </iframe> : ""}

      {currentVideo.codeHtmlIframe ?
        <div className='row d-flex justify-content-center player-wrapper'
          dangerouslySetInnerHTML={{ __html: currentVideo.codeHtmlIframe }} ></div>
        : ""}
    </div>
  );

});

