import React, { Component } from "react";

import '../../Css/Nav.css'

export default class PhotoShape extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };

    }

    render() {
        return (
            <div className="container-fluid mt-2 mb-2 " >
                <div className="row">
                    <label className="title">Photo Shap</label>
                </div>
                <div className="row d-flex justify-content-around">
                    <div className="col" >
                        <div className="row" style={{
                            width: "30px",
                            height: "30px",
                            background: "#F5F5FA",
                            borderRadius: "60%"
                        }}></div>
                        <label className="row title">Round</label>

                    </div>


                    <div className="col" >
                        <div className="row" style={{
                            width: "30px",
                            height: "30px",
                            background: "#B8B8B8",
                            borderRadius: "0%"
                        }}></div>
                        <label className="row title">Square</label>

                    </div>

                    <div className="col" >
                        <div className="row" style={{
                            width: "30px",
                            height: "30px",
                            background: "#B8B8B8",
                            borderRadius: "20%"
                        }}></div>
                        <label className="row title">Radius</label>
                    </div>

                    <div className="col" >
                        <div className="row" style={{
                            width: "30px",
                            height: "25px",
                        }}></div>
                        <input type="number" className="row input "
                            min="0" max="6"
                        ></input>
                    </div>
                </div>


            </div>

        );
    }
}
