
import { connect } from 'react-redux';
import React from 'react';
import Carousel from 'react-material-ui-carousel'
import { Paper } from '@material-ui/core'
import { actions } from '../../Redux/actions/Action'
import { Button, Modal } from 'react-bootstrap';

function mapStateToProps(state) {

  return {
    gallery: state.gallerySlide.currentGallery,
    showBullets: state.gallerySlide.currentGallery.slide.showBullets,
    showNav: state.gallerySlide.currentGallery.slide.showNav,
    autoPlay: state.gallerySlide.currentGallery.slide.showPlayButton,
    animation: state.gallerySlide.currentGallery.slide.animation,
    timeout: state.gallerySlide.currentGallery.slide.timeout,
    screenSize: state.card.screenSize,
    imageListDefult:state.gallerySlide.imageListDefult

  };
}
function mapDispatchToProps(dispatch) {
  return {
    setHeightGallery: (height) => dispatch(actions.setHeightGallery(height))
  }
};
export default connect(mapStateToProps, mapDispatchToProps)(function SlideGallery(props) {
  const { 
    gallery,
    showBullets,
    showNav,
    autoPlay,
    animation,
    timeout,
    imageListDefult
  } = props;
  
  return (
  gallery.galleries && gallery.galleries.length ? 
   <Carousel
    indicators={showBullets} 
    // navButtonsAlwaysVisible={showNav}
     autoPlay={autoPlay} 
     animation={animation} 
     timeout={timeout} >
      {
        gallery.galleries.map((item, i) => <Item key={i} item={item}  {...props} />)
      }
    </Carousel>:
    <Carousel
    indicators={showBullets} 
    // navButtonsAlwaysVisible={showNav}
     autoPlay={autoPlay} 
     animation={animation} 
     timeout={timeout} >
      {
       imageListDefult.map((item, i) => <Item key={i} item={item}  {...props} />)
      }
    </Carousel>
  )
}
)
function Item(props) {

  const { gallery, screenSize, showState, preview } = props;
  let height = "200", w = parseInt(window.innerWidth);
  if (!showState || preview) {
    if (screenSize === 1)
      height = "45vh";
    else
      height = "150px";
  }
  else {
    if (w >= 600)
      height = "45vh";
    else
      height = "150px";
  }
  return (
    <>
      <Paper>
        <div className="row" style={{
           backgroundImage:"url(" + props.item.backgroundImage+")",
           border: "1px solid " + gallery.borderColor, 
           height: height, backgroundSize: 'cover',
           backgroundRepeat: 'no-repeat'
        }} > </div>
      </Paper>
    </>
  )
}
