import React, { Component } from "react";
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';

import { onChangeHandlerImage } from '../../BL/StyleCard';
import './GalleryNav.css'




export class ApLoadImage extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        const {
            gallery,
            setGalleryCangeImages,
            setalertStatuseUpLodeImge,
            addImgGallery, 
            setImgModal,
            setcurrentIndex,
        } = this.props;

        return (
            // 974*400
            < >
                <div className="row m-0 p-0">
                    <label className=" title">Load Image</label>
                </div>
                <div className="row m-0 p-0">
                    <div
                        className="mr-1 ImageLoad border"
                        onClick={() => {
                            setImgModal({
                                show:true,
                                imgId: "#fileLoaderGallery",
                                func: addImgGallery,
                                type: 'img',
                                aspectX:gallery.state=== "grid"?(1/gallery.grid.cols)*700:246,
                                aspectY: gallery.state=== "grid"?gallery.grid.cellHeightDesktop:150,
                                cropShape: 'rect',
                                scrollBack:500

                            })
                        }}
                    >
                        <input
                            type="file" name="files" title="Load File"
                            id="fileLoaderGallery"
                            hidden
                            accept="image/png, image/jpeg"
                            onChange={e => onChangeHandlerImage(e.target.files[0], addImgGallery, null, setalertStatuseUpLodeImge, 'img')}></input>
                    </div>
                    <div className="Load">{
                        gallery.galleries ? gallery.galleries.map((image, i) => {
                            return (
                                <div
                                    className="mr-1 border ImageLoad" key={i}
                                    onClick={() => { 
                                        setImgModal({
                                            show:true,
                                            imgId: "#" + i,
                                            func: setGalleryCangeImages,
                                            type: 'img',
                                            aspectX:gallery.state=== "grid"?(1/gallery.grid.cols)*700:246,
                                            aspectY: gallery.state=== "grid"?gallery.grid.cellHeightDesktop:150,
                                            cropShape: 'rect',
                                            currBackground: gallery.galleries[i].backgroundImage,
                                            scrollBack:500

                                        })
                                        setcurrentIndex(i) }}
                                    style={{ backgroundImage: "url(" + image.backgroundImage + ")" }}
                                >
                                    <input
                                        type="file" name="files" title="Load File"
                                        id={i}
                                        hidden
                                        accept="image/png, image/jpeg"
                                        onChange={e => { setcurrentIndex(i); onChangeHandlerImage(e.target.files[0], setGalleryCangeImages, null, setalertStatuseUpLodeImge, 'img') }}></input>
                                </div>
                            )
                        }) : ""
                    }
                    </div>

                </div>

            </>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        gallery: state.gallerySlide.currentGallery,
        screenSize: state.card.screenSize,
    };
}

const mapDispatchToProps = (dispatch) => ({
    addImgGallery: (img) => dispatch(actions.addImgGallery(img)),
    setalertStatuseUpLodeImge: (status) => dispatch(actions.setalertStatuseUpLodeImge(status)),
    setGalleryCangeImages: (status) => dispatch(actions.setGalleryCangeImages(status)),
    setcurrentIndex: (status) => dispatch(actions.setcurrentIndex(status)),
    setImgModal:(imgDetails) => dispatch(actions.setImgModal(imgDetails))
})
export default connect(mapStateToProps, mapDispatchToProps)(ApLoadImage);