import produce from 'immer';
import createReducer from "./reducerUtils";
import { newReview } from '../../BL/Restarts'
import { setFile } from '../../Files/file'

const initialState = {

    currentReviews: {
        IndexReveiws: 0,
        profileShow: true,
        roleShow: true,
        reveiwShow: true,
        titleReveiws: 'Reveiws',
        showMoreReveiws: false,
       

        reveiwsList: [{
            reveiwsDetails: {
                full_name: "",
                role: "",
                message_reveiws: "",
                ReveiwShowDelete: false,
            },
            reveiwsStyle: {
                bodyReveiws: {
                    background: "#ffffff",
                    textAlign: "center",
                },
                profile_reveiws: {
                    backgroundImage: "",
                    backgroundSize: "cover",
                    position: "relative",
                    width: "40px",
                    height: "40px",
                    borderColor: "#ffc107",
                    borderRadius: "50%",
                    zIndex: 1,
                    indexFile: 0
                },
                input_card_fullName: {
                    color: "rgb(0,0,0)",
                    // textAlign: "center",
                    font: "700 28px/26px Roboto",
                    letterSpacing: "0.84px",
                    textTransform: "capitalize",
                    fontSize: "12px",
                    overflow: "hidden"
                },
                input_card_rule: {
                    // textAlign: "center",
                    color: "rgb(0,0,0)",
                    font: "Regular 20px/26px Roboto",
                    letterSpacing: "0.6px",
                    fontSize: "11px",
                    marginTop: "-5px",

                },
                message_reveiws_background: {
                    background: "#ffffff1f",
                    // width: "100%",
                    fontSize: "11px",
                    border: "none",
                    color: "#00000",
                    lineHeight: 1.2,
                    height: "45px"
                },
                label_reveiws_background: {
                    textAlign: "left",
                    background: "#ffffff1f",
                    width: "100%",
                    fontSize: "0.8em",
                    border: "1px solid",
                    borderColor: "#ffffff00",
                    borderRadius: "10px",
                    color: "#00000",
                    minHeight: "100px!important",
                    maxWidth: "100%",
                    wordWrap: "break-word !important",
                },
                buttonReveiws: {
                    width: "200px",
                    color: "rgb(255, 255, 255)",
                    background: "#eb2ffb",
                    borderRadius: "10px",
                    fontSize: "22px !important",
                    textAlign: "center",
                    height: "100%",
                }
            }
        }],
        styleColorArrow: "rgba(0, 0, 0, 0.27)",
    }
   

};
const reveiws = {
    setCurrentReviews(state, action) {
        state.currentReviews = action.payload;
    },
    setFullNameReveiws(state, action) {
        state.currentReviews.reveiwsList[state.currentReviews.IndexReveiws].reveiwsDetails.full_name = action.payload;
    },
    setRuleReveiws(state, action) {
        state.currentReviews.reveiwsList[state.currentReviews.IndexReveiws].reveiwsDetails.role = action.payload;
    },
    setMessageReveiws(state, action) {
        state.currentReviews.reveiwsList[state.currentReviews.IndexReveiws].reveiwsDetails.message_reveiws = action.payload;
    },
    setIndexReveiws(state, action) {

        state.currentReviews.IndexReveiws = action.payload;
    },
    //show
    setRoleReveiwsShow(state, action) {
        state.roleShow = !state.roleShow;
    },
    setReveiwShow(state, action) {
        state.currentReviews.reveiwShow = action.payload;
    },
    setonClickshowMore(state, action) {
        state.currentReviews.showMoreReveiws = !state.currentReviews.showMoreReveiws;
    },

    setReveiwShowDelete(state, action) {
        state.currentReviews.reveiwsList[state.currentReviews.IndexReveiws].reveiwsDetails.ReveiwShowDelete = true;
    },

    // setCoverReveiwsStyle(state, action) {
    //     state.currentReviews.reveiwsList[state.currentReviews.IndexReveiws].
    //         reveiwsStyle.profile_reveiws.backgroundImage = action.payload;
    //     state.currentCard.vCardStyle.profile.backgroundImage = action.payload.url;
    //     setFile({ profile: action.payload.file })
    // }, 

    //style reveiws
    setCoverReveiwsBackground(state, action) {
        state.currentReviews.reveiwsList[state.currentReviews.IndexReveiws].
            reveiwsStyle.profile_reveiws.backgroundImage = action.payload;
    },

    setBackgroundReveiwsStyle(state, action) {
        state.currentReviews.reveiwsList[state.currentReviews.IndexReveiws].reveiwsStyle.bodyReveiws.background = action.payload;
    },

    setColorFullNameReveiws(state, action) {
        state.currentReviews.reveiwsList[state.currentReviews.IndexReveiws].reveiwsStyle.input_card_fullName.color = action.payload;
        state.currentReviews.reveiwsList[state.currentReviews.IndexReveiws].reveiwsStyle.input_card_rule.color = action.payload;
    },

    addNewReveiws(state, action) {
        const review = newReview();
        state.currentReviews.reveiwsList.push(review)
        // state.currentReviews.IndexReveiws = state.currentReviews.reveiwsList.length - 1;
    },
    setReveiwsTitle(state, action) {
        state.currentReviews.titleReveiws = action.payload;
    },
    setstyleColorArrowReviews(state, action) {
        state.currentReviews.styleColorArrow = action.payload;
    },
}
export default produce((state, action) => createReducer(state, action, reveiws), initialState);
