import React, { Component } from 'react';
import './custom.css'
import { Provider } from 'react-redux';
import keys from './config/keys';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import store from './Redux/Stors/AgentStore';
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { fas } from '@fortawesome/free-solid-svg-icons'
import NewWrap from './components/Frame/NewWrap'
import Card from './components/card/Card'
import CardNameModal from './Modals/CardNameModal'
import { connect } from 'react-redux';
import CardPreview from './components/card/CardPreview';
import ErrorPage404 from './components/ErrorPage404/ErrorPage404';
import Information from './components/Statistics/InformationTemp'
import { actions } from "./Redux/actions/Action";

library.add(fab, fas)

class App extends Component {
  render() {

    return (
      <div className="container-fluid p-0 m-0  ">
        <Provider store={store}>
          <Router>
            <Switch>
              {/* preview */}
              <Route path={`/preview/:screenSize`} exact>
                <div className="container-fluid " style={{ height: "100vh", overflow: "auto" }}>
                  <CardPreview {...this.props}></CardPreview>
                </div>
              </Route>
              <Route
                path="/admin/:userName/statistics"
                component={Information}
              >
              </Route>

              <Route path="/admin/:userName/:cardName" >
                <NewWrap />
              </Route>

              <Route path="/admin/:userName" >
                <NewWrap />
              </Route>

             

              {/* view */}
              <Route path={`/:uid/:cardId`} exact>
                <div 
                  id="View"
                  style={{
                    height: "100vh",
                    overflowY: "auto",
                    background: "rgb(235, 234, 234)"
                  }}>
                  <Card showState={true}//view
                  ></Card>
                </div>
              </Route>
              <Route path='/' exact component={() => {
                window.location.href = `${keys.API_URL_BASE_CLIENT}/login`;
                return null;
              }} />
              <Route component={ErrorPage404}/>

            </Switch>

          </Router>

        </Provider>
      </div >
    );
  }
}
const mapStateToProps = (state) => {
  return {
    screenSize: state.card.screenSize,
    card: state.card,
    uploadFileFlag: state.modal.imgModal.show
  };
}

const mapDispatchToProps = (dispatch) => ({
  setImgModalShow: (status) => dispatch(actions.setImgModalShow(status)),
})
export default connect(mapStateToProps, mapDispatchToProps)(App);