export function newReview() {
    return {
        reveiwsDetails: {
            full_name: "",
            role: "",
            message_reveiws: "",
            ReveiwShowDelete: false,
        },
        reveiwsStyle: {

            bodyReveiws: {
                background: "#ffffff",
                textAlign: ""
            },
            profile_reveiws: {
                backgroundImage: "",
                backgroundSize: "cover",
                position: "relative",
                width: "40px",
                height: "40px",
                border: "none",
                borderColor: "#ffc107",
                borderRadius: "50%",
                zIndex: 1,
                indexFile: 0
            },

            input_card_fullName: {
                color: "#00000",
                // textAlign: "center",
                font: "700 28px/26px Roboto",
                letterSpacing: "0.84px",
                textTransform: "capitalize",
                fontSize: "12px",
                overflow: "hidden"
            },
            input_card_rule: {
                // textAlign: "center",
                color: "#00000",
                font: "Regular 20px/26px Roboto",
                letterSpacing: "0.6px",
                fontSize: "11px",
                marginTop: "-10px"

            },
            message_reveiws_background: {
                background: "#ffffff1f",
                // width: "100%",
                fontSize: "11px",
                border: "none",
                color: "#00000",
                lineHeight: 1.2,
                height: "100%"
            },
            label_reveiws_background: {
                textAlign: "left",
                background: "#ffffff1f",
                width: "100%",
                fontSize: "0.8em",
                border: "1px solid",
                borderColor: "#ffffff00",
                borderRadius: "10px",
                color: "#00000",
                minHeight: "100px!important",
                maxWidth: "100%",
                wordWrap: "break-word !important",
            },
            buttonReveiws: {
                width: "80%",
                color: "rgb(255, 255, 255)",
                background: "#eb2ffb",
                borderRadius: "10px",
                fontSize: "22px !important",
                textAlign: "center",
                height: "100%",
            }
        }
    }
}
