export function newGallery() {
    return {
        title: 'Gallery',
        currentIndex: -1,
        galleryShow: true,
        borderColor: "transperent",
        state: "grid",
        grid: {
            cols: 3,
            cellHeight: 50,
            cellHeightDesktop: 150,
            useStyles: {
                root: {
                    width: "90%",
                    margin: "auto",
                    flexWrap: 'wrap',
                    justifyContent: 'space-around',
                    overflow: 'auto',
                    backgroundColor: 'transparent'
                },
                gridList: {
                    flexWrap: 'wrap',
                    transform: 'translateZ(0)',
                },
            }
        },
        slide: {
            lazyLoad: false,
            showThumbnails: true,
            thumbnailPosition: "",
            showPlayButton: true,
            showBullets: true,
            showNav: true,
            thumbnailPosition: 'bottom',
            slideDuration: 150,
            slideInterval: 1000,
            autoPlay: true,
            animation: "slide",
            timeout: 500,
            checkedRadio: true,
        },
        galleries: []
    }
}

