import React, { Component } from "react";
import { actions } from '../Redux/actions/Action';
import { connect } from 'react-redux';
import StyleInputCard from './helpComponents/StyleInputCard'
import IconShapStyle from './helpComponents/IconShapStyle'
import '../Css/Nav.css'




class VcardStyleSocialMedia extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };

    }

    render() {
        return (
            // <div className="mt-2 " >
            <>
                <div className="row ">
                    <label className="title">Links Style</label>
                </div>
                <div className="row">
                    <StyleInputCard
                        title="Icon Color"
                        background={this.props.vCardStyle.bodyCardMobile.socialMedia.borderColor}
                        onchangeColor={this.props.changeIconColor}
                        color={this.props.screenSize === 0 ? this.props.vCardStyle.bodyCardMobile.socialMedia.borderColor : this.props.vCardStyle.body_card_desktop.socialMedia.borderColor}
                    > </StyleInputCard>
                </div>

                <div className="row">
                    <StyleInputCard
                        title="Icon Fill Color"
                        background={this.props.vCardStyle.bodyCardMobile.socialMedia.background}
                        onchangeColor={this.props.setIconFillColor}
                        color={this.props.screenSize === 0 ? this.props.vCardStyle.bodyCardMobile.socialMedia.background : this.props.vCardStyle.body_card_desktop.socialMedia.background}
                    > </StyleInputCard>
                </div>
                {/* <div className="row mt-3">
                    <div className="col m=0 p-0 title">Number of columns</div>
                    <input type="number" className="col-2 m-0 p-0 input "
                        min="0" max={this.props.maxSocialMedia}
                        value={this.props.numSocialMedia}
                        onChange={(e)=>this.props.changnumSocialMedia(e.target.value)}
                    ></input>
                </div> */}
                
                <div className="row ">
                    <IconShapStyle
                        changShapRadius={this.props.changShapRadius}
                    ></IconShapStyle>
                </div>
               
                <div className="row  mb-2">
                    <div className="col m-0 p-0 title">Border width</div>
                    <input type="number" className="col-2 m-0 p-0 input "
                        min="0" 
                        max={this.props.maxSocialMedia}
                        value={this.props.numBroderIcon}
                        onChange={(e) => this.props.changnBorderWidthIcon(e.target.value)}
                    ></input>
                </div>
            </>
            // </div >
        );
    }
}
const mapStateToProps = (state) => {
    return {
        maxSocialMedia: state.card.maxSocialMedia,
        numSocialMedia: state.card.currentCard.numSocialMedia,
        numBroderIcon: state.card.currentCard.numBroderIcon,
        vCardStyle: state.card.currentCard.vCardStyle,
        screenSize: state.card.screenSize
    };
}

const mapDispatchToProps = (dispatch) => ({
    changeTitleIconColor: (color) => dispatch(actions.setTitleIconColor(color)),

    changeIconColor: (color) => dispatch(actions.setIconColor(color)),

    changShapRadius: (radius) => dispatch(actions.setShapRadius(radius)),

    changnumSocialMedia: (num) => dispatch(actions.setnumSocialMedia(num)),

    setIconFillColor: (num) => dispatch(actions.setIconFillColor(num)),

    changnBorderWidthIcon: (num) => dispatch(actions.setBorderWidthIcon(num))
})
export default connect(mapStateToProps, mapDispatchToProps)(VcardStyleSocialMedia);