import React, { Component } from "react";
import { actions } from '../Redux/actions/Action';
import { connect } from 'react-redux';
import StyleInputCard from './helpComponents/StyleInputCard'
import { OnOff } from './helpComponents/OnOff'
import TextAlign from '../components/textAlign'
import $ from 'jquery'
import '../Css/Nav.css'
import '../Css/Card.css';
import '../Css/Templet/Templets.css'

class VcardCoverStyle extends Component {

    render() {

        const {
            setImgModal,
            setCoverDesktopcardStyle,
            changeLogoStyle,
            changeProfileStyle,
            changeCoverStyle,
            setTitleTextAlignBody,
            setTitleColorBody,
            cssObj
        } = this.props;
        return (
            <div onClick={(e) => e.stopPropagation()}>
                <div className="mt-3">
                    <TextAlign onClickAligh={setTitleTextAlignBody}></TextAlign>
                </div>
                <div className="row mt-3">
                    <StyleInputCard
                        title="Background title"
                        background={ this.props.vCardStyle.bodyCardMobile.styleTitleBody.color}
                        onchangeColor={setTitleColorBody}
                        color={this.props.screenSize === 0 ? this.props.vCardStyle.bodyCardMobile.styleTitleBody.color : this.props.vCardStyle.body_card_desktop.styleTitleBodyDesktop.color}
                    > </StyleInputCard>
                </div>

                <div className="row mt-3">
                    <StyleInputCard
                        title="Background Color"
                        background={this.props.vCardStyle.bodyCardMobile.input_card_fullName.color}
                        onchangeColor={this.props.changeBodyColor}
                        color={this.props.screenSize === 0 ? this.props.vCardStyle.bodyCardMobile.body_card.background : this.props.vCardStyle.body_card_desktop.body_card_desktop_inline.background}
                    > </StyleInputCard>
                </div>

                {/* <div className="row">
                    <label className="title">Logo/Photo</label>
                </div> */}
                <div className="row">
                    <label className="title mt-3"> Cover image</label>
                </div>
                <div
                    className={"row agentPic border d-flex justify-content-center " + this.props.vCardStyle.cssObjTamplate.cover}
                    id="navCaver"
                    onClick={() => {
                        setImgModal({
                            show: true,
                            imgId: "#fileLoaderCover",
                            func: [changeCoverStyle, setCoverDesktopcardStyle],
                            type: 'div',
                            aspectX: 266,
                            aspectY: cssObj.cover ? 590 * (((parseFloat(cssObj.cover.height)) / 100)) : 3,
                            aspectXDesktop: $(window).width(),
                            aspectYDesktop: cssObj.cover ? $(window).height() * (((parseFloat(cssObj.cover.height)) / 100)) : 3,
                            cropShape: 'rect',
                            currBackground: cssObj.cover ? cssObj.cover.backgroundImage : "",
                            // currBackground: cssObj.cover ? screenSize===0?cssObj.cover.backgroundImage:cssObj.coverDesktop.backgroundImage : ""

                        });
                    }}
                    style={{ background: this.props.vCardStyle.cover.backgroundImage, margin: "auto", backgroundSize: "cover !importent", }}
                ></div>
                <div className="row">
                    <label className="title mt-3">logo Image</label>
                </div>
                <div className={"row cardLogo border d-flex justify-content-center " + this.props.vCardStyle.cssObjTamplate.logo}
                    style={{ background: this.props.vCardStyle.logo.backgroundImage, margin: "auto", backgroundSize: "cover !importent", }}
                    onClick={() => {
                        setImgModal({
                            show: true,
                            imgId: "#fileLoaderLogo",
                            func: changeLogoStyle,
                            type: 'div',
                            aspectX: 160,
                            aspectY: 160,
                            cropShape: 'round',
                            currBackground: this.props.vCardStyle.logo.backgroundImage ? this.props.vCardStyle.logo.backgroundImage : ""
                        });
                    }}
                ></div>

                <div className="row mt-3">
                    <StyleInputCard
                        title="Border Color Logo"
                        background={this.props.vCardStyle.logo.borderColor}
                        onchangeColor={this.props.setBackgroundRadiosLogo}
                        color={this.props.screenSize === 0 ? this.props.vCardStyle.logo.borderColor : this.props.vCardStyle.logo.borderColor}
                    > </StyleInputCard>
                </div>


                <div className="row ">
                    <label className="title mt-3">profile Image</label>
                </div>
                <div className={"row cardProfile border d-flex justify-content-center " + this.props.vCardStyle.cssObjTamplate.profile}
                    style={{
                        background: this.props.vCardStyle.profile.backgroundImage,
                        margin: "auto",
                    }}
                    onClick={() => {
                        setImgModal({
                            show: true,
                            imgId: "#fileLoaderProfile",
                            func: changeProfileStyle,
                            type: 'div',
                            aspectX: 58,
                            aspectY: 58,
                            cropShape: 'round',
                            currBackground: this.props.vCardStyle.profile.backgroundImage ? this.props.vCardStyle.profile.backgroundImage : ""
                        });
                    }}
                >
                </div>


                <div className="row input_footer mb-2"  >
                    <div className="col-8 p-1">
                        <label id="aboutMe" className="title" >profile Show</label>
                    </div>
                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            idElement="#showProfile"
                            cheak={this.props.vCardStyle.profileShow}
                            onChangeOnOff={this.props.changeProfileShow}
                            type={"profile"}
                        ></OnOff>
                    </div>
                </div>


                <div className="row mt-3">
                    <StyleInputCard
                        title="Border Color Profile "
                        background={this.props.vCardStyle.profile.borderColor}
                        onchangeColor={this.props.setBackgroundRadiosProfile}
                        color={this.props.screenSize === 0 ? this.props.vCardStyle.profile.borderColor : this.props.vCardStyle.profile.borderColor}
                    > </StyleInputCard>
                </div>


              
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        vCardStyle: state.card.currentCard.vCardStyle,
        screenSize: state.card.screenSize,
        cssObj: state.card.currentCard.vCardStyle,

    };
}
const mapDispatchToProps = (dispatch) => ({
    changeBodyColor: (color) => dispatch(actions.setBodyColor(color)),
    changeCoverStyle: (style) => dispatch(actions.setCovercardStyle(style)),
    setCoverDesktopcardStyle: (style) => dispatch(actions.setCoverDesktopcardStyle(style)),
    changeProfileShow: (cheaked, socialMediaType) => dispatch(actions.setprofileShow(cheaked)),
    changeProfile: (name) => dispatch(actions.setprofile(name)),
    setBackgroundRadiosLogo: (name) => dispatch(actions.setBackgroundRadiosLogo(name)),
    setBackgroundRadiosProfile: (name) => dispatch(actions.setBackgroundRadiosProfile(name)),
    setImgModal: (status) => dispatch(actions.setImgModal(status)),
    changeLogoStyle: (style) => dispatch(actions.setLogocardStyle(style)),
    changeProfileStyle: (style) => dispatch(actions.setProfilecardStyle(style)),
    setTitleTextAlignBody: (align) => dispatch(actions.setTitleTextAlignBody(align)),
    setTitleColorBody: (color) => dispatch(actions.setTitleColorBody(color)),
    // changeRuleColor: (color) => dispatch(actions.setRuleColor(color)),

})
export default connect(mapStateToProps, mapDispatchToProps)(VcardCoverStyle);