const mapStateToProps = (state) => {
    return {
        agentDetails: state.agentDetails.currentAgentForCard,
        vCardStyle: state.card.currentCard.vCardStyle,
        validation: state.validation,
        vCardStyle: state.card.currentCard ? state.card.currentCard.vCardStyle : null,
        screenSize: state.card.screenSize
    };
}

const mapDispatchToProps = (dispatch) => ({

    changeFullName: (name) => dispatch(actions.setFullname(name)),

    changeFullNameShow: () => dispatch(actions.setFullnameShow()),

    changeRule: (rule) => dispatch(actions.setRule(rule)),
    changeRuleShow: () => dispatch(actions.setRuleShow()),
    changeAboutShow: () => dispatch(actions.setAboutShow()),

    changeMobile: (mobile) => dispatch(actions.setMobile(mobile)),

    changePersonalEmail: (email) => dispatch(actions.setPersonalemail(email)),

    changeAboutTitle: (title) => dispatch(actions.setAbouttitle(title)),

    setAboutConnect: (connect) => dispatch(actions.setAboutConnect(connect)),

    setAbouttitle: (content) => dispatch(actions.setAbouttitle(content)),

    setPersonalmassegeTitle: (title) => dispatch(actions.setPersonalmassegeTitle(title)),


    changeMobileTitle: (mobileTitle) => dispatch(actions.setMobiletitle(mobileTitle)),
    setPersonalmassegeConnect: (content) => dispatch(actions.setPersonalmassegeConnect(content)),

    validationOnChange: (event) => dispatch(actions.handleChange(event)),
    changeFullNameTitleColor: (color) => dispatch(actions.setFullNameTitleColor(color)),
    changeRuleColor: (color) => dispatch(actions.setRuleColor(color)),
    openTextAboutMe: (status) => dispatch(actions.openTextAboutMe(status)),

    setExpanded: (expanded) => dispatch(actions.setExpanded(expanded)),
    setTextAlignAbout: (align) => dispatch(actions.setTextAlignAbout(align)),
    setColorTextAbout: (color) => dispatch(actions.setColorTextAbout(color)),
    setFullnameTextAlign: (textAlign) => dispatch(actions.setFullnameTextAlign(textAlign)),
    setRuleTextAlign: (textAlign) => dispatch(actions.setRuleTextAlign(textAlign)),

})

export default connect(mapStateToProps, mapDispatchToProps)(AgentDetails, SelecComponent);





