import React, { Component } from "react";
import { actions } from '../Redux/actions/Action';
import { connect } from 'react-redux';
import StyleInputCard from './helpComponents/StyleInputCard'
import { OnOff } from './helpComponents/OnOff'
import '../Css/Nav.css'
import '../Css/Card.css'
import AgentDetail from './helpComponents/AgentDetail'
import TextAlign from '../components/textAlign'


class Footer extends Component {

    render() {

        console.log(this.props.vCardStyle)
        return (
            <div className="container-fluid mt-2 " >
                <div className="row">
                    <label className="title" >form settings</label>
                </div>
                <div className="row ">
                    <input
                        className="col-8 p-0 input"
                        value={this.props.vCardStyle.bodyCardMobile.footerMobile.titleFooter}
                        onChange={(e) => { this.props.changeTitleFooterMobile(e.target.value) }}
                    ></input>

                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            idElement="footerShow"
                            cheak={this.props.vCardStyle.titleFooterShow}
                            onChangeOnOff={this.props.setTitleFooterShow}
                            // type={"showgallery"}
                        ></OnOff>
                    </div>
                </div>

                <div className=" mt-4">
                    <TextAlign onClickAligh={this.props.setTitleFooterTextAlign}></TextAlign>
                </div>
                <div className="row input_footer"  >
                    <div className="col-8 p-1">
                        <label id="aboutMe" className="title" style={{ textAlign: "center" }}>Full Name</label>
                    </div>
                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            // title={this.props.agentDetails.fullName}
                            onChange={this.props.changeFullNameFooter}
                            cheak={this.props.vCardStyle.fullNameFooterShow}
                            onChangeOnOff={this.props.setFullNameFooterShow}
                            type={"fullName"}
                            idElement="#FooterfullName"

                        ></OnOff>
                    </div>
                </div>
                <div className="row input_footer">
                    <div className="col-8 p-1">
                        <label id="aboutMe" className="title">Mobile</label>
                    </div>
                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            // title={this.props.agentDetails.fullName}
                            onChange={this.props.changeMobileFooter}
                            cheak={this.props.vCardStyle.mobileFooterShow}
                            onChangeOnOff={this.props.setMobileFooterShow}
                            type={"fullName"}
                            idElement="#FooterMobile"

                        ></OnOff>
                    </div>
                </div>
                <div className="row input_footer">
                    <div className="col-8 p-1">
                        <label id="aboutMe" className="title">Email</label>
                    </div>
                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            // title={this.props.agentDetails.fullName}
                            onChange={this.props.changePersonalEmailFooter}
                            cheak={this.props.vCardStyle.personalEmailFooterShow}
                            onChangeOnOff={this.props.setPersonalEmailFooterShow}
                            type={"fullName"}
                            idElement="#FooterPersonalEmail"

                        ></OnOff>
                    </div>
                </div>


                <div className="row input_footer">
                    <div className="col-8 p-1">
                        <label id="aboutMe" className="title">Message</label>
                    </div>
                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            onChange={this.props.changeMessageFooter}
                            cheak={this.props.vCardStyle.messageFooterShow}
                            onChangeOnOff={this.props.setMessageFooterShow}
                            type={"fullName"}
                            idElement="#Footer"
                        ></OnOff>
                    </div>
                </div>


                <div className="row row mt-4">
                    <StyleInputCard
                        title="Titel Style"
                        background={this.props.vCardStyle.bodyCardMobile.input_footer_title.color}
                        onchangeColor={this.props.changeTitleFooterColorMobile}
                        color={this.props.vCardStyle.bodyCardMobile.input_footer_title.color}
                    > </StyleInputCard>
                </div>

                <div className="row ">
                    <StyleInputCard
                        title="title Button Style"
                        background={this.props.vCardStyle.bodyCardMobile.input_footer_button.color}
                        onchangeColor={this.props.changeButtonFooterColorMobile}
                        color={this.props.vCardStyle.bodyCardMobile.input_footer_button.color }
                    > </StyleInputCard>
                </div>

                <div className="row">
                    <StyleInputCard
                        title="Background Button"
                        background={this.props.vCardStyle.bodyCardMobile.input_footer_button.background}
                        onchangeColor={this.props.changeButtonFooterBackgroundMobile}
                        color={this.props.vCardStyle.bodyCardMobile.input_footer_button.background}
                    > </StyleInputCard>
                </div>

                <div className="row ">
                    <StyleInputCard
                        title="Background Footer"
                        background={this.props.vCardStyle.bodyCardMobile.background_footer.background}
                        onchangeColor={this.props.changeFooterBackgroundMobile}
                        color={this.props.vCardStyle.bodyCardMobile.background_footer.background}
                    > </StyleInputCard>
                </div>
                <div className="row ">
                    <StyleInputCard
                        title="Background Input"
                        background={this.props.vCardStyle.bodyCardMobile.input_footer_background.background}
                        onchangeColor={this.props.changeInputFooterBackgroundMobile}
                        color={this.props.vCardStyle.bodyCardMobile.input_footer_background.background}
                    > </StyleInputCard>
                </div>
                {/* <div className="row mb-3">
                    <AgentDetail
                        header="Title"
                        title={this.props.vCardStyle.bodyCardMobile.footerMobile.titleFooter !== "" ? this.props.vCardStyle.bodyCardMobile.footerMobile.titleFooter : ""}
                        // title={this.props.agentDetails.fullName}
                        onChange={this.props.changeTitleFooterMobile}
                        cheak={this.props.vCardStyle.titleFooterShow}
                        onChangeOnOff={this.props.setTitleFooterShow}
                        type={"fullName"}
                    ></AgentDetail>
                </div> */}
                <div className="row mb-3">
                    <AgentDetail
                        header="Button"
                        title={this.props.vCardStyle.bodyCardMobile.footerMobile.buttonFooter !== "" ? this.props.vCardStyle.bodyCardMobile.footerMobile.buttonFooter : ""}
                        cheak={this.props.vCardStyle.buttonFooterShow}
                        onChange={this.props.changeButtonFooterMobile}
                        onChangeOnOff={this.props.setButtonFooterShow}
                        hiddenOnOff={false}
                        type={"fullName"}
                        idElement="#Footer"
                    ></AgentDetail>
                </div>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        vCardStyle: state.card.currentCard.vCardStyle,
        screenSize: state.card.screenSize,
        // changeProfile: state.currentCard.vCardStyle.profileShow

    };
}
const mapDispatchToProps = (dispatch) => ({
    changeBodyColor: (color) => dispatch(actions.setBodyColor(color)),
    changeCoverStyle: (style) => dispatch(actions.setCovercardStyle(style)),
    changeProfileShow: (cheaked, socialMediaType) => dispatch(actions.setprofileShow(cheaked)),
    changeProfile: (name) => dispatch(actions.setprofile(name)),
    changeTitleFooter: (name) => dispatch(actions.setTitleFooter(name)),
    changeTitleFooterMobile: (name) => dispatch(actions.setTitleFooterMobile(name)),
    setTitleFooterShow: (cheaked, socialMediaType) => dispatch(actions.setTitleFooterShow(cheaked)),
    changeFullNameFooter: (name) => dispatch(actions.setFullNameFooter(name)),
    setFullNameFooterShow: (cheaked, socialMediaType) => dispatch(actions.setFullNameFooterShow(cheaked)),
    changeMobileFooter: (name) => dispatch(actions.setMobileFooter(name)),
    setMobileFooterShow: (cheaked, socialMediaType) => dispatch(actions.setMobileFooterShow(cheaked)),
    changePersonalEmailFooter: (name) => dispatch(actions.setPersonalEmailFooter(name)),
    setPersonalEmailFooterShow: (cheaked, socialMediaType) => dispatch(actions.setPersonalEmailFooterShow(cheaked)),
    changeMessageFooter: (name) => dispatch(actions.setMessageFooter(name)),
    setMessageFooterShow: (cheaked, socialMediaType) => dispatch(actions.setMessageFooterShow(cheaked)),
    changeButtonFooter: (name) => dispatch(actions.setButtonFooter(name)),
    changeButtonFooterMobile: (name) => dispatch(actions.setButtonFooterMobile(name)),

    setButtonFooterShow: (cheaked, socialMediaType) => dispatch(actions.setButtonFooterShow(cheaked)),
    changeTitleFooterColor: (color) => dispatch(actions.setTitleFooterColor(color)),
    changeButtonFooterColor: (color) => dispatch(actions.setButtonFooterColor(color)),
    changeButtonFooterBackground: (color) => dispatch(actions.setButtonFooterBackground(color)),
    changeFooterBackground: (color) => dispatch(actions.setFooterBackground(color)),
    changeInputFooterBackground: (color) => dispatch(actions.setInputFooterBackground(color)),
    changeTitleFooterColorMobile: (color) => dispatch(actions.setTitleFooterColorMobile(color)),
    changeButtonFooterColorMobile: (color) => dispatch(actions.setButtonFooterColorMobile(color)),
    changeButtonFooterBackgroundMobile: (color) => dispatch(actions.setButtonFooterBackgroundMobile(color)),
    changeFooterBackgroundMobile: (color) => dispatch(actions.setFooterBackgroundMobile(color)),
    changeInputFooterBackgroundMobile: (color) => dispatch(actions.setInputFooterBackgroundMobile(color)),
    // changeRuleColor: (color) => dispatch(actions.setRuleColor(color)),
    setFooterShow: (status) => dispatch(actions.setFooterShow(status)),
    setTitleFooterTextAlign: (textAlign) => dispatch(actions.setTitleFooterTextAlign(textAlign)),

})
export default connect(mapStateToProps, mapDispatchToProps)(Footer);