import React from 'react';
import { connect } from 'react-redux';

import Card from './Card'
import './Card.css'

const mapStateToProps = (state) => {
    return {
        currentCard: state.card.currentCard
    };
}

// e 1
export default connect(mapStateToProps)(function CardMobile(props) {
    const { preview, screenSizep, currentCard } = props
    return (
        <div
            id="mobile"
            className={preview?"container mobile mobile_preview":"container mobile"}
            style={{background:currentCard.vCardStyle.bodyCardMobile.body_card.background}}
        >
            {!preview?
                <Card showState={false} ></Card>:
                <Card  screenSizep={screenSizep} showState={true} preview={true} ></Card>}
        </div>
    );
})


