import React from "react";
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function mapStateToProps(state) {
    return {
        tamplate: state.tamplate
    };
}
function mapDispatchToProps(dispatch) {
    return {
        setVcardStyle:(tamlate)=>{dispatch(actions.setVcardStyle(tamlate))}
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(function Tamplate(props) {
    const {tamplate, setVcardStyle}=props
    return (
        <>
            <button onClick={()=>{setVcardStyle(tamplate.tamplate1)}}>tamplate 1</button>
        </>
    );
});
