import React, { Component } from "react";
import { Switch, Route, Link } from "react-router-dom";
import { connect } from 'react-redux';
import { actions } from "../../Redux/actions/Action";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Button, Modal } from 'react-bootstrap';
import MenuIcon from '@material-ui/icons/Menu';
import Configurator from './Configurator'
import Logo from '../Frame/assets/logo.png'
import Loading from '../Loading/Loading'
import AllCards from '../AllCards'
import Card from '../card/Card'
import Demo from '../../Modals/UploadImg/uploadFile'
import Tamplate from '../TamplateCard/Tamplate'
import Tooltip from '@material-ui/core/Tooltip';
import CardMobile from '../card/CardMobile'
import CardTablet from '../card/CardTablet'
import Slide from '@material-ui/core/Slide';
import keys from '../../config/keys';
import '../../Css/NewWrap.css'
import '../../Modals/Css/UploadFile.css'
import CopyUrl from '../helpComponents/CopyUrl'
class NewWrap extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            configurator: false,
            navLeft: true,
            nuck: false,
            moreApps: false,
            showPreview: false,

        }

    }


    opendAndCloseConfigurator = () => {
        if (!this.state.nuck)
            this.setState({ configurator: !this.state.configurator })
    }

    serNuck = () => {
        this.setState({ nuck: !this.state.nuck })
    }
    setMoreApp = () => {
        this.setState({ moreApps: !this.state.moreApps })
    }

    opendAndCloseLeftNav = () => {
        let element = document.getElementById("leftNav");
        if (this.state.navLeft) {
            element.classList.remove("col-1");
            element.classList.add("col-2");
        }
        else {
            element.classList.remove("col-2");
            element.classList.add("col-1");
        }
        this.setState({ navLeft: !this.state.navLeft })
    }

    componentDidMount() {


        let w = parseInt(window.innerWidth);
        if (w >= 600) {
            this.setState({ configurator: false });
        }
        else {
            this.setState({ configurator: true });
        }
    }

    // preview
    setShowPreview = (status) => {
        this.setState({ showPreview: status })
    }
    handleClose = () => { this.setShowPreview(false); }
    handleShow = () => {
        this.setShowPreview(true);
    }

    render() {
        return (
            <div className="container-fluid mt-2 bodyAll"
                onClick={() => this.props.setImgModalShow(false)}>
                {/* nav header */}
                <div className="row navHeader d-flex justify-content-between">
                    <div className="col container navHeader_container d-flex flex-column justify-content-center ">
                        <div className="row d-flex justify-content-around">

                            {/* logo and bar */}
                            <div className="col container ">
                                <div className="row d-flex justify-content-between">
                                    <div className={this.props.publishStatus !== 2 ? "col-3" : "col-12"}>

                                        <div className=" mt-1 ml-3 p-0 d-flex justify-content-start">
                                            <Tooltip title="statistics" arrow>
                                                <Link to={`/${'admin'}/${this.props.user.userName}/statistics`} >
                                                    <img style={{
                                                        width: "40px",
                                                        height: "40px"
                                                    }}
                                                        src={Logo} alt="knowme"
                                                        className="img-fluid rounded-circle d-flex justify-content-center">

                                                    </img>
                                                </Link>
                                            </Tooltip>
                                        </div>

                                    </div>
                                    {
                                        this.props.publishStatus !== 2 ?
                                            <div className="col-9 d-flex justify-content-around edit_url">
                                             
                                                <CopyUrl url={`${keys.API_URL_BASE_CLIENT}/${this.props.user.userName}/${this.props.card.cardName}`}></CopyUrl>
                                                <label className="d-flex align-items-center">
                                                    {keys.API_URL_BASE_CLIENT}/{this.props.user.userName}/</label>
                                                <input
                                                    className="d-flex align-items-center"
                                                    onBlur={(e) => {
                                                        if (!e.currentTarget.contains(e.relatedTarget) && this.props.card.cardName !== e.target.value) {
                                                            this.props.checkUniqueCardName(
                                                                {
                                                                    cardName: this.props.card.cardName,
                                                                    index: this.props.card.currentCardIndex
                                                                })
                                                        }
                                                    }}
                                                    style={{
                                                        display: "block!important",
                                                        background: "transparent",
                                                        border: "none",
                                                        marginTop: "-7px"
                                                    }}

                                                    value={
                                                        this.props.card.cardName
                                                    }
                                          

                                             
                                                    onChange={(e) => {
                                                    
                                                        this.props.editCurrentCardName(e.target.value);
                                                        this.props.ErrorCurrentMassageCardName("")
                                                    }}
                                                />
                                        </div> : ""
                                    }
                                    <label className="err-message" style={{
                                        font: " 16px / 17px Roboto",
                                        marginLeft: "160px"
                                    }}>{
                                            this.props.cards[this.props.card.currentCardIndex] ?
                                                this.props.cards[this.props.card.currentCardIndex].errCardName :
                                                this.props.card.errCardName
                                        }</label>

                                </div>
                            </div>

                            {/* nav items */}
                            <div className="col-5 d-flex flex-column align-items-center justify-content-center "  >

                                <div className="d-flex justify-content-around align-items-center " style={{ width: "100px" }}>
                                    <Modal
                                        size='xl'
                                        show={this.state.showPreview}
                                        onHide={this.handleClose}
                                        centered
                                    >
                                        <Modal.Header closeButton></Modal.Header>

                                        <Modal.Body
                                            style={{ height: this.props.screenSize === 1 ? "600px" : "" }}
                                        >
                                            {
                                                this.props.screenSize === 0 ?
                                                <CardMobile screenSizep={this.props.screenSize} preview={true} /> :
                                                this.props.screenSize === 1 ?
                                                <Card screenSizep={this.props.screenSize} showState={true} preview={true} /> :
                                                <CardTablet screenSizep={this.props.screenSize} showState={true} preview={true} />
                                            }

                                        </Modal.Body>
                                        <Modal.Footer>
                                            <Button variant="secondary" onClick={this.handleClose}>
                                                Close
                                            </Button>
                                        </Modal.Footer>
                                    </Modal>
                                    <Tooltip title="Mobile mode" arrow>
                                        <div
                                            className={
                                                this.props.screenSize === 0 ?
                                                    "selected align-items-center mode-icon" :
                                                    "align-items-center mode-icon"
                                            }
                                            onClick={() => this.props.setScreenSize(0)}
                                        >
                                            <FontAwesomeIcon
                                                icon={['fas', 'mobile-alt']}
                                                color="#3A405E"
                                                className={this.props.screenSize === 0 ? "selected-for-icon" : ""}
                                            ></FontAwesomeIcon>

                                        </div>
                                    </Tooltip>

                                    <Tooltip title="Tablet mode" arrow>
                                        <div
                                            className={
                                                this.props.screenSize === 2 ?
                                                    "selected align-items-center mode-icon" :
                                                    "align-items-center mode-icon"
                                            }
                                            onClick={() => this.props.setScreenSize(2)}
                                        >
                                            <FontAwesomeIcon
                                                icon={['fas', 'tablet']}
                                                color="#3A405E"
                                                className={this.props.screenSize === 2 ? "selected-for-icon" : ""}
                                            ></FontAwesomeIcon>

                                        </div>
                                    </Tooltip>

                                    <Tooltip title="Desktop mode" arrow>
                                        <div
                                            className={
                                                this.props.screenSize === 1 ?
                                                    "selected align-items-center mode-icon" :
                                                    "align-items-center mode-icon"
                                            }
                                            onClick={() => this.props.setScreenSize(1)}
                                        >
                                            <FontAwesomeIcon
                                                title="Desktop mode"
                                                icon={['fas', 'desktop']}
                                                color="#3A405E"
                                                className={this.props.screenSize === 1 ? "selected-for-icon" : ""}

                                            />
                                        </div>
                                    </Tooltip>
                                    <Tooltip title="Preview" arrow>
                                        <div className="align-items-center mode-icon">
                                            <FontAwesomeIcon title="Preview" icon='eye' onClick={this.handleShow} color="#3A405E" />
                                        </div>
                                    </Tooltip>
                                </div>
                            </div>

                            {/* profile bar thumbtack */}
                            <div className="col container">
                                <div className="row d-flex flex-row-reverse">
                                    <div className="col-1 mr-4 mt-1  d-none d-sm-none d-md-block " onClick={() => { this.opendAndCloseConfigurator() }}>
                                        <MenuIcon fontSize="large"></MenuIcon>
                                    </div>
                                    {this.state.nuck ?
                                        <div className="col-2 mt-2 d-none d-sm-none d-md-block" onClick={this.serNuck}>

                                            <FontAwesomeIcon icon='thumbtack' />
                                        </div> :
                                        <div className="col-2 mt-2 d-none d-sm-none d-md-block d-flex justify-content-center" onClick={this.serNuck}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="31" height="31"
                                                viewBox="0 0 31.621 31.621"> <g fill="#3a405e">
                                                    <path d="M14.874,10.694l-.611-5.9H16.37a1.2,1.2,0,0,0,1.2-1.2V1.2A1.2,1.2,0,0,0,16.37,0H2.795A1.2,1.2,0,0,0,1.6,1.2v2.4a1.2,1.2,0,0,0,1.2,1.2H4.9l-.611,5.9C1.87,11.82,0,13.838,0,16.37a1.2,1.2,0,0,0,1.2,1.2H7.985v5.191a.4.4,0,0,0,.042.179l1.2,2.4a.4.4,0,0,0,.714,0l1.2-2.4a.4.4,0,0,0,.042-.179V17.568h6.788a1.2,1.2,0,0,0,1.2-1.2C19.165,13.816,17.269,11.808,14.874,10.694Z" transform="translate(18.069) rotate(45)" />
                                                </g></svg>
                                        </div>}

                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                {/* body */}
                <div className="row body bg-light">

                    {/* contain here put your component */}
                    <div
                        className="col content rounded m-3 p-0"
                        id="wapper"
                    >
                        {/* img modal */}

                        {this.props.modal.imgModal.show ?
                            <Slide
                                direction="left"
                                in={this.props.modal.imgModal.show}
                                mountOnEnter
                            >
                                < div className="overlay">
                                    <div className=" row h-100 m-0" >
                                        <div className="overlay_onclos d-flex justify-content-center align-items-center"
                                            onClick={() => this.props.setImgModalShow(false)}>
                                            <FontAwesomeIcon icon={['fas', 'grip-lines-vertical']} color="#3A405E"></FontAwesomeIcon>
                                        </div>

                                        <div
                                            onClick={(e) => e.stopPropagation()}
                                            className="overlay_container d-flex justify-content-center  align-items-center">
                                            {this.props.modal.imgModal.show ? <Demo /> : ""}
                                        </div>
                                    </div>

                                </div>
                            </Slide>
                            : ""}
                        {this.props.load ? <Loading></Loading> : ""}
                        <Switch>
                            <Route path={`/${"admin"}/${this.props.user.userName}/AllCards`} exact>
                                <AllCards></AllCards>
                            </Route>
                            <Route path={`/${this.props.user.userName}/Tamplates`} exact>
                                <Tamplate></Tamplate>
                            </Route>
                            <Route path="/" >
                                {
                                    this.props.screenSize === 0 ?
                                    <CardMobile /> :
                                    this.props.screenSize === 1 ?
                                    <Card showState={false} /> :
                                    <CardTablet />
                                }
                            </Route>

                        </Switch>
                        {/* <Card></Card> */}
                    </div>

                    {/* confogurator */}
                    <div className=" container configurator" hidden={this.state.configurator} >

                        <Configurator></Configurator>
                    </div>
                </div>
            </div >
        );
    }
}


const mapStateToProps = (state) => {
    return {
        currentCard: state.card.currentCard,
        load: state.card.load,
        user: state.user,
        validation: state.validation,
        vCardStyle: state.card.currentCard ? state.card.currentCard.vCardStyle : null,
        screenSize: state.card.screenSize,
        modal: state.modal,
        cardNameModal: state.modal.cardNameModal,
        card: state.card.currentCard,
        cards: state.card.cards,
        publishStatus: state.card.publishStatus,
    };
}

const mapDispatchToProps = (dispatch) => ({
    setScreenSize: (status) => dispatch(actions.setScreenSize(status)),
    setImgModalShow: (status) => dispatch(actions.setImgModalShow(status)),
    setCardNameShow: (status) => dispatch(actions.setCardNameShow(status)),
    checkUniqueCardName: (cardNameAndIndex) => dispatch(actions.checkUniqueCardName(cardNameAndIndex)),
    ErrorMassageCardName: (errMessage) => dispatch(actions.ErrorMassageCardName(errMessage)),
    setCurrentCardName: (cardName) => dispatch(actions.setCurrentCardName(cardName)),
    editCardName: (obj) => dispatch(actions.editCardName(obj)),
    ErrorCurrentMassageCardName: (errMessage) => dispatch(actions.ErrorCurrentMassageCardName(errMessage)),
    editCurrentCardName: (obj) => dispatch(actions.editCurrentCardName(obj)),
})

export default connect(mapStateToProps, mapDispatchToProps)(NewWrap);







