import React from 'react';
import { actions } from '../Redux/actions/Action';
import { Modal, Button } from 'react-bootstrap';
import { connect } from 'react-redux'


const CardNameModal=(props)=>{

    
    const {
        cardNameModal,
        setCardNameShow,
        card,
        cards,
        checkUniqueCardName,
        setErrorMassage,
        editCardName,
        setCurrentCardName

    }=props;

    
    const setCardName=(e)=>{
        if(cardNameModal.cardIndex===-1){
            setCurrentCardName(e.target.value);
        }
        else{
            editCardName({
                cardName:e.target.value,
                index:cardNameModal.cardIndex
            });
        }
        setErrorMassage("");
    }

    const onSave=()=>{
        let cardName=cardNameModal.cardIndex===-1?card.cardName:
        cards[cardNameModal.cardIndex].cardName
        checkUniqueCardName({cardName:cardName,index:cardNameModal.cardIndex})
    }

        return (
            <>
                <Modal
                    show={cardNameModal.show}
                    onHide={()=>setCardNameShow({
                        status:false,
                        index:-1
                    })}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>
                            {
                                cardNameModal.cardIndex===-1?"Save Card":"Edit Card"
                            }
                        </Modal.Title>
                    </Modal.Header>

                    <Modal.Body>
                        <div className="form-group">
                            <label>Enter a name for the card</label>
                            <input
                                onChange={setCardName}
                                className="form-control"
                                value={
                                    cards[cardNameModal.cardIndex]?
                                    cards[cardNameModal.cardIndex].cardName:
                                    card.cardName
                                }
                            />
                            <label className="err-message">{cardNameModal.errorMessage}</label>
                        </div>
                    </Modal.Body>

                    <Modal.Footer>
                        <Button
                            variant="secondary"
                            onClick={()=>setCardNameShow({
                                status:false,
                                index:-1
                            })}
                        >
                            Close
                        </Button>
                        <Button variant="primary" onClick={onSave}>
                            Save
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>
        );
}


const mapDispatchToProps=(dispatch)=>{

    return {
        setCardNameShow:(status)=>dispatch(actions.setCardNameShow(status)),
        checkUniqueCardName:(cardNameAndIndex)=>dispatch(actions.checkUniqueCardName(cardNameAndIndex)),
        setErrorMassage:(errMessage)=>dispatch(actions.setErrorMassage(errMessage)),
        setCurrentCardName:(cardName)=>dispatch(actions.setCurrentCardName(cardName)),
        editCardName:(obj)=>dispatch(actions.editCardName(obj)),
    };
}

const mapStateToProps=(state)=>{
    return {
        cardNameModal:state.modal.cardNameModal,
        card:state.card.currentCard,
        cards:state.card.cards
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(CardNameModal);
