import $ from 'jquery'

export function onChangeHandlerImage(event, changeUrlFunc, elementId, setalertStatuseUpLodeImge, type = 'div') {
    if (event) {
        if (event.size > 20000) {
            setalertStatuseUpLodeImge(true);
            return;
        }
        let reader = new FileReader();
        if (event != null) {
            reader.onloadend = () => {
                const url = reader.result;
                console.log(reader.result)
                if (type === 'img')
                    changeUrlFunc({ url: url, file: event });
                else
                    changeUrlFunc({ url: `url(${url})`, file: event });
                if (elementId)
                    $(elementId).css("background-size", "cover");
            }
            reader.readAsDataURL(event)
        }
    }
    else
        return

}
export function acceptCropperSize(width,height,func)
{
    func(width,height);
}

export function convertImgToBase64(event, changeUrlFunc) {
    if (event) {
        let reader = new FileReader();
        if (event != null) {
            reader.onloadend = () => {
                let url = reader.result;
                changeUrlFunc(url);
            }
            reader.readAsDataURL(event)
        }
    }
    else
        return;
}

export function changeImgBackground(event, url, elementId, changeUrlFunc, type = 'div') {
    if (event.size > 900000) {
      let massege= 'The image is too large,a maximum size of 900 megabytes is enabled '
        return massege;

     }
    if (type === 'img')
        changeUrlFunc({ url: url, file: event });
    else
        changeUrlFunc({ url: `url(${url})`, file: event });
    if (elementId)
        $(elementId).css("background-size", "100% 100%");
    return null;
}

export function openfileDialog(loadId, openVcardStyle = null) {
    $(loadId).click((event) => { event.stopPropagation(); });
    $(loadId).click();
    if (openVcardStyle)
        openVcardStyle();
}
