import React from 'react';
import { OnOff } from './OnOff'
import '../../Css/helpComponentCss/AgentDetail.css'
import '../../Css/Nav.css'


const AgentDetail=(props)=> {
    const { 
        placeholder,
        title,
        onChange,
        header,
        cheak,
        onChangeOnOff,
        type,
        typeInput,
        name,
        error,
        validationOnChange,
        hiddenOnOff = true
    } = props;
    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-8 p-0">
                    <label id={title} className="title">{header}</label>
                </div>
                {
                    hiddenOnOff ?
                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff idElement={header} cheak={cheak} onChangeOnOff={onChangeOnOff} typeElement={type}></OnOff>
                    </div> : ""
                }
            </div>
            <div className="row p-0 d-flex flex-column">
                <input
                    id={type}  
                    className={
                        header==="Personal Email"?
                        "text-transform-none input":"input"
                    }
                    placeholder={placeholder?placeholder:"Title"}
                    value={title !== "" ? title : ""}
                    onChange={(e) => { onChange(e.target.value) }}
                    // validationOnChange({ event: e,textName:type})}}
                    type={typeInput ? typeInput : "text"}
                    name={name ? name : "name"}
                    required
                    noValidate
                />
                {/* <span className='error'>{error}</span> */}

            </div>
        </div>
    );
};
export default AgentDetail;