import React from 'react';
import './Mail.css';



export function Mail(props) {
    const { header, body } = props;
    return (
        <div id="mail_text">
            <div style={{ display: "flex", 
            flexWrap: " wrap",
            justifyContent: 'flex-end', 
            marginRight: " -15px", }}>
                <img src={'https://files.knowme.page/views/img/logoknow.png'}
                    style={{
                        backgroundSize: "cover",
                        width: "323px",
                        height: "74px",
                        marginTop: "20px",
                    }}
                    alt="knowme.page"></img>
            </div>
            <div style={{ display: "flex", 
            flexWrap: " wrap", marginRight: " -15px" }}>
                <div style={{ flexBasis: "0", flexGrow: "1", maxWidth: " 100%" }}>
                    <img src={'https://files.knowme.page/views/img/MailLogo.png'}
                        style={{ width: "500px",
                         height: "400px",
                           marginTop: "40px", 
                           maxWidth: " 100%" }}
                           alt="knowme.page"></img>
                </div>
                <div style={{ flexBasis: " 0", flexGrow: "1", maxWidth: " 100%" }}>
                    <div style={{
                        textAlign: "left",
                        font: " normal normal bold 30px/45px Roboto",
                        letterSpacing: "0px", color: " #2B0118",
                        marginTop: "40px",
                        marginLeft: " 50px",
                        display: "flex",
                        flexWrap: " wrap",
                        marginRight: " -15px",
                    }}>{header}
                    </div>
                    <div style={{
                        textAlign: "left",
                        font: "normal normal 300 12pt/30pt Roboto",
                        letterSpacing: " 0pt",
                        color: "#2B0118", marginLeft: "50px",
                        marginTop: " 20px"
                    }}>
                        {body.map((mail) => (
                            <div style={{
                                display: "flex",
                                flexWrap: "wrap",
                                justifyContent: 'flex-end'
                            }}>{mail}</div>
                        ))}
                    </div>
                    <div style={{
                        display: "flex",
                        flexWrap: " wrap",
                        marginRight: " -15px",
                        justifyContent: 'center'
                    }}>
                        <img style={{
                            maxWidth: "100%",
                            height: " auto",
                            width: "150px!important",
                            border: " 0pt solid #C23181",
                            marginTop: "20px",
                            marginLeft: "210px"
                        }}
                            src={'https://files.knowme.page/views/img/Line.png'}></img>
                    </div>
                    <div style={{
                        display: "flex",
                        flexWrap: " wrap",
                        marginRight: " -15px",
                        textAlign: " left",
                        font: " normal normal bold 14pt/35pt Roboto", letterSpacing: "0pt",
                        justifyContent: 'center',
                        color: " #C23181", marginLeft: "400px", marginTop: "20px"
                    }}>knowme.page
                    </div>
                </div>
            </div>
            <img style={{
                maxWidth: "100%",
                height: " auto",
                position: "fixed",
                left: " 0",
                bottom: "0",
                width: "100%"
            }}
                src={'https://files.knowme.page/views/img/footer_mail.png'}></img>
        </div>


    );

};
