import Background from '../assets/defualtUploadImage.svg';

export const styles = theme => {
  return ({
    bachdrop: {
      background: "#5a63632b"
    },
    crop: {
      containerClassName:
      {
        background:"blue"
      }
    },
    cropContainer: {
      position: 'relative',
      width: '40%',
      height: 150,
      margin: 'auto',
      marginButtom: "5px",
      backgroundImage: "url(" + Background + ") !important" ,
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      [theme.breakpoints.up('sm')]: {
        height: 400,
      },
    },
    cropButton: {
      color: '#fff',
      backgroundColor: '#3A405E',
      display: 'inline-flex',
      justifyContent: 'center',
      alignItems: 'center',
      height: '30px',
      minWidth: '120px',
      borderRadius: '16px',
      fontFamily: 'Roboto", "Helvetica", "Arial", sans-serif',
      fontWeight: '500',
      lineHeight: 1.75,
      letterSpacing: '0.02857em',
      flexShrink: 0,
      marginLeft: 16
    },
    cropButtonWihgt: {
      color: '#3A405E',
      backgroundColor: '#FFFF',
      borderColor: '#3A405E'

    },
    controls: {
      width: '40%',
      height: '50px',
      margin: 'auto',
      marginTop: "5px",
      background: '#EBEAEA',
      padding: 16,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'stretch',
      [theme.breakpoints.up('sm')]: {
        flexDirection: 'row',
        alignItems: 'center',
      },
    },
    sliderContainer: {
      display: 'flex',
      justifyContent: 'center',
      flex: '1',
      alignItems: 'center',
    },
    sliderLabel: {
      color: '#384364',
      margin: '16px',
      [theme.breakpoints.down('xs')]: {
        minWidth: 65,
      },
    },
    slider: {
      color: "#384364",
      padding: '22px 0px',
      [theme.breakpoints.up('sm')]: {
        flexDirection: 'row',
        alignItems: 'center',
        margin: '0 16px',
      },
    },
    InputNumber: {
      color: "#384364",
      maxWidth: "50px",
      backgroundColor: 'transparent',
      border: 0,
      outline: 0,
      // textAlign: 'center',
      opacity: 1
    }
  })
}


