import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { red } from '@material-ui/core/colors';
import '../../Css/Alert.css';



function mapStateToProps(state) {

  return {
    alertStatuseDelete: state.alert.alertStatuseDelete,
    alertStatusePublish: state.alert.alertStatusePublish,
    alertStatuseSucsses: state.alert.alertStatuseSucsses,
    alertStatuseUpLodeImge: state.alert.alertStatuseUpLodeImge,
    publishStatus: state.card.publishStatus,
    indexDelet:state.card.indexDelet,

  };
}
function mapDispatchToProps(dispatch) {
  return {
    setalertStatuseDelete: (status) => dispatch(actions.setalertStatuseDelete(status)),
    setalertStatusePublish: (status) => dispatch(actions.setalertStatusePublish(status)),
    setalertStatuseSucsses: (status) => dispatch(actions.setalertStatuseSucsses(status)),
    setalertStatuseUpLodeImge: (status) => dispatch(actions.setalertStatuseUpLodeImge(status)),
    deleteCard: (insdex) => dispatch(actions.deleteCard(insdex)),
    addNewCurrentCard: (preview) => { return dispatch(actions.saveCard(preview)) },
    setPublishStatus: (status) => dispatch(actions.setPublishStatus(status)),


  }
};

export default connect(mapStateToProps, mapDispatchToProps)(function AlertDelete(props) {
  const [open, setOpen] = React.useState(false);
  const { alertStatuseDelete, alertStatusePublish, alertStatuseSucsses, setalertStatuseDelete, setalertStatusePublish, setalertStatuseSucsses,  indexDelet, deleteCard, addNewCurrentCard,setPublishStatus ,setalertStatuseUpLodeImge,alertStatuseUpLodeImge} = props;




  return (
    <div>

      <Dialog style={{ background: red }}
        open={alertStatuseDelete || alertStatusePublish || alertStatuseSucsses || alertStatuseUpLodeImge}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{alertStatuseDelete === true ? "Are you sure you want to delete this Card?" : alertStatuseSucsses === true ? "The operation was successful" :
          alertStatusePublish === true ? "Are you sure you want to Publish ?" :  alertStatuseUpLodeImge === true ? "It is possible to upload an image up to 200KB":""}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          {alertStatuseDelete === true ? <Button onClick={(e) => {e.stopPropagation(); e.preventDefault(); deleteCard( indexDelet); setalertStatuseDelete(false); setalertStatuseSucsses(true) }} color="primary">
            Ok
          </Button> : ""}
          {alertStatusePublish === true ? <Button onClick={(e) => {e.preventDefault(); addNewCurrentCard(false);
            setalertStatusePublish(false); setalertStatuseSucsses(true);}} color="primary">
            Ok
          </Button> : ""}
          <Button onClick={(e) => { e.preventDefault(); setalertStatuseDelete(false); setalertStatusePublish(false); setalertStatuseSucsses(false); setalertStatuseUpLodeImge(false)}} color="primary" autoFocus>
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
});
