import React from 'react';
import { onChangeHandlerImage } from '../../BL/StyleCard';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
function mapStateToProps(state) {

    return {
    };
}
function mapDispatchToProps(dispatch) {
    return {
        setalertStatuseUpLodeImge: (status) => dispatch(actions.setalertStatuseUpLodeImge(status)),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(function UploadFile(props) {

    const { id, changeCoverStyle, idElementChange, setalertStatuseUpLodeImge } = props;
    return (
        <div className="container-fluid">
            <input
                type="file" id={id} name="files" title="Load File" hidden
                accept="image/png, image/jpeg"
                onChange={e => onChangeHandlerImage(e.target.files[0], changeCoverStyle, idElementChange, setalertStatuseUpLodeImge)}
            />
        </div >
    )
})