// One row with scroll
import React from 'react';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { OnOff } from '../helpComponents/OnOff'
import Slider from '@material-ui/core/Slider';
import AgentDetail from '../helpComponents/AgentDetail'



const mapStateToProps = (state) => {
    return {
        currentVideo: state.video.currentVideo,

    };
}

const mapDispatchToProps = (dispatch) => ({

    setplayingVideo: () => dispatch(actions.setplayingVideo()),
    setSpeedVideo: (speed) => dispatch(actions.setSpeedVideo(speed)),
    setUrlVideo: (url) => dispatch(actions.setUrlVideo(url)),
    setVideoTitle: (title) => dispatch(actions.setVideoTitle(title)),
    setVideoShow: () => dispatch(actions.setVideoShow()),
    setCodeHtmlIframe:(text) => dispatch(actions.setCodeHtmlIframe(text)),

})

export default connect(mapStateToProps, mapDispatchToProps)(function VideoNav(props) {

    const { setCodeHtmlIframe, currentVideo, setVideoShow, setUrlVideo, setVideoTitle } = props;
    return (
        <>
            <div className="container-fluid">
                <div className="row ">
                    <label className="title">Video Settings</label>
                </div>
                <div className="row ">
                    <input className="col-8 p-0 input"
                        value={currentVideo.titleVideo}
                        onChange={(e) => { setVideoTitle(e.target.value) }}
                    ></input>
                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            idElement="VideoShow"
                            cheak={currentVideo.videoShow}
                            onChangeOnOff={setVideoShow}
                            type={"VideoShow"}
                        ></OnOff>
                    </div>



                    {/* <input type="number" className="col-3 m-0 p-0 input d-flex align-items-start"
                        value={currentVideo.videoCard.playbackRate}
                        onChange={(event) => setSpeedVideo(event.target.value)}

                    ></input> */}
                </div>
                <div className="row mt-3">
                    <AgentDetail
                        header="enter url video"
                        title={currentVideo.videoCard.url}
                        placeholder="Each Line Url"
                        onChange={setUrlVideo}
                        typeInput="text"
                        name="urlVideo"
                        // error={this.props.validation.errors.mobile}
                        // validationOnChange={this.props.validationOnChange}
                        hiddenOnOff={false}

                    ></AgentDetail>
                </div>
                <div className="row mt-3">
                    <label className="title">Code Html</label>
                </div>
                <div className="row mt-1">
                <textarea
                    className="form-control p-0  m-0 textAgentDetailsTextArea texteara-border"
                    placeholder="Enter Code HTML"
                    onChange={(e) => setCodeHtmlIframe(e.target.value)}
                ></textarea>
            </div>
        </div>
        </>
    );
})