import React, { Component } from "react";
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import './GalleryNav.css'
import ApLoadImage from './ApLoadImage'
import PhotoShape from './PhotoShape'
import { OnOff } from '../helpComponents/OnOff'
import GridGalleryNav from './GridGalleryNav'
import NavSlideGallery from './NavSlideGallery'
import StyleInputCard from '../helpComponents/StyleInputCard'
import InputColor from 'react-input-color';
import TextAlign from '../../components/textAlign'



export class GalleryNav extends Component {
    constructor(props) {
        super(props);
        this.state = {
            //  const [color, setColor] = react.useState({});
        };

    }

    render() {
        const { gallery, setStateGallery, setGalleryShow, setBorderColorImage, setGallerytitle} = this.props;
        return (
            <div className="container-fluid mt-2 " onClick={(e) => e.stopPropagation()} >
               
                <div className="row ">
                    <label className="title"> Gallery Details</label>
                </div>
                <div className="row ">
                    <input
                        className="col-8 p-0 input"
                        value={gallery.title}
                        onChange={(e) => { setGallerytitle(e.target.value) }}
                    ></input>
                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            idElement="galleryShow"
                            cheak={gallery.galleryShow}
                            onChangeOnOff={setGalleryShow}
                            type={"showgallery"}
                        ></OnOff>
                    </div>
                </div>
               
                <div className="row ">
                    <RadioGroup row aria-label="position" name="position" defaultValue="top">
                        <FormControlLabel value="Grid" control={<Radio color="primary" />} label="Grid" className=" title"
                            checked={gallery.state === 'grid' ? true : false}
                            onChange={(event) => setStateGallery(event.target.checked ? 'grid' : 'slide')} />
                        <FormControlLabel value="Slide" control={<Radio color="primary" />} label="Slide" className=" title"
                            checked={gallery.state === 'slide' ? true : false}
                            onChange={(event) => setStateGallery(event.target.checked ? 'slide' : 'grid')} />
                    </RadioGroup>
                </div>
                {gallery.state === 'grid' ?
                    <GridGalleryNav></GridGalleryNav> :
                    <NavSlideGallery></NavSlideGallery>}

                <div className="row">
                    <ApLoadImage></ApLoadImage>
                </div>
                {/* <div className="row ">
                    <PhotoShape
                    ></PhotoShape>
                </div> */}

                <div className="row mt-3 ">
                    <StyleInputCard
                        title="Stroke Photo Style"
                        background={gallery.borderColor}
                        onchangeColor={setBorderColorImage}
                        color={gallery.borderColor}
                    > </StyleInputCard>
                </div>

                {/* <div className="row ">
                    <button className="title"> huhuh</button>
                </div> */}

            </div >
        );
    }
}
const mapStateToProps = (state) => {
    return {
        gallery: state.gallerySlide.currentGallery
    };
}

const mapDispatchToProps = (dispatch) => ({
    setStateGallery: (state, index) => dispatch(actions.setStateGallery(state)),
    setGalleryShow: (stateShow) => dispatch(actions.setGalleryShow(stateShow)),
    setBorderColorImage: (name) => dispatch(actions.setBorderColorImage(name)),
    setGallerytitle: (title) => dispatch(actions.setGallerytitle(title)),
    

})
export default connect(mapStateToProps, mapDispatchToProps)(GalleryNav);