import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { useClipboard } from 'use-clipboard-copy';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import '../../Css/helpComponentCss/IconCard.css'


function mapStateToProps(state) {

    return {
        alertLink: state.alert.alertLink,
    };
}
function mapDispatchToProps(dispatch) {
    return {
        setalertLink: () => dispatch(actions.setalertLink()),

    }
};


export default connect(mapStateToProps, mapDispatchToProps)(function ShareUrl(props) {
    const clipboard = useClipboard({ copiedTimeout: 800 });
    const { url } = props;
    useEffect(() => {
        setTimeout(() => {

        }, 3000);
    }, [clipboard]);

    return (
        <div className="container-fluid m-1">
            <div className="row">
                <FontAwesomeIcon
                    title="coped url"
                    color={clipboard.copied ? "rgb(107, 255, 142)" : "gray"}
                    onClick={(e) => {
                        { clipboard.copy(url) }
                    }
                    }
                    icon={['fas', 'copy']}
                ></FontAwesomeIcon>
            </div>
            {clipboard.copied ?
                <div className="row copy_url"><label className="copy_url_Title d-flex justify-content-center align-items-center" >URL copied! </label></div>
                : ""}
        </div>
    );
})


