import React from 'react';
import { IconPickerItem } from 'react-fa-icon-picker'
import { connect } from 'react-redux';

import './Card.css'
import '../../Css/default_card_designs.css'

class SocialMediaCard extends React.Component {
    
    render() {
        const {    
            showState,
            setFooterSendEmailShowMobile,
            css,
            onClickFunc,
            styleIcon,
            title,
            icon,
            url,
            card
         } = this.props;

        return (
            !showState?
            <div className={css} onClick={(e) => { e.stopPropagation(); onClickFunc() }}>
                <div className="row d-flex justify-content-center">
                    <div
                        onClick={setFooterSendEmailShowMobile ? (e) => { e.stopPropagation(); setFooterSendEmailShowMobile() } : null}
                        style={styleIcon}
                        title={title}
                        className=" d-flex align-items-center justify-content-center icon"
                    >
                        <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""}style={{cursor: "pointer"}} />
                    </div>
                </div>
            </div> : icon === "FaWhatsapp" ?
            <a 
                className={css+" d-flex align-items-start justify-content-center"} 
                href={`https://wa.me/${url}?text=${card.currentCard.whatsAppWelcomMassage}`}
            >‏
                <div className="row d-flex justify-content-center">
                    <div
                        onClick={setFooterSendEmailShowMobile ? (e) => { setFooterSendEmailShowMobile() } : ""}
                        style={styleIcon}
                        title={title}
                        className="d-flex align-items-center justify-content-center"
                    >
                        <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""} />

                    </div>
                </div>
            </a>
            : icon === "FaPhoneAlt" ?
            <a className={css} href={url}>
                <div className="row d-flex justify-content-center">
                    <div
                        onClick={setFooterSendEmailShowMobile ? 
                            (e) => { setFooterSendEmailShowMobile() } : ""}
                        style={styleIcon}
                        title={title}
                        className="d-flex align-items-center justify-content-center"
                    >
                        <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""} />

                    </div>
                </div>
                </a> :
                url ?
                <a 
                    className={css} onClick={() => {
                    let sendUrl = url
                    if (!url.startsWith("https://"))
                        sendUrl = "https://" + url
                    window.open(sendUrl, "_blank")
                }}>
                <div className="row d-flex justify-content-center">
                    <div
                        onClick={setFooterSendEmailShowMobile ? (e) => { setFooterSendEmailShowMobile() } : ""}
                        style={styleIcon}
                        title={title}
                        className="d-flex align-items-center justify-content-center"
                    >
                        <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""} />

                    </div>
                </div>
            </a> : <div className={css} >
                            <div className="row d-flex justify-content-center aaa">
                                <div
                                    onClick={setFooterSendEmailShowMobile ? (e) => { setFooterSendEmailShowMobile() } : ""}
                                    style={styleIcon}
                                    title={title}
                                    className="d-flex align-items-center justify-content-center"
                                >
                                    <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""} />

                                </div>
                            </div>
                        </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        card: state.card
    };
}
export default connect(mapStateToProps)(SocialMediaCard);