import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { useClipboard } from 'use-clipboard-copy';




function mapStateToProps(state) {

    return {
        alertLink: state.alert.alertLink,
    };
}
function mapDispatchToProps(dispatch) {
    return {
        setalertLink: () => dispatch(actions.setalertLink()),

    }
};


export default connect(mapStateToProps, mapDispatchToProps)(function ShareUrl(props) {
    const clipboard = useClipboard({ copiedTimeout: 750 });
    const { setalertLink, url, styleUrl } = props;
    useEffect(() => {
        setTimeout(() => {
            setalertLink();
        }, 3000);
    }, [clipboard]);
    return (
        <button title="copy link" style={{
            background: clipboard.copied ? "rgb(107, 255, 142)" : "#FF5F5F",
            color: clipboard.copied ? "rgb(113, 111, 118)" : "white" ,
            height: "20px", width: "250px", textAlign: "center"
        }}
            onClick={(e) => { clipboard.copy(url) }}>
            {clipboard.copied ? 'Copied' : url}
        </button>
    );
})


