import React, { useState, useCallback, useEffect } from 'react'
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles'
import getCroppedImg from './cropImage'
import { styles } from './styles'
import CropperComponent from './cropper'
import { openfileDialog, convertImgToBase64, changeImgBackground } from '../../BL/StyleCard'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import './uploadImg.css'

const Demo = (props) => {
  const {
    classes,
    imgModal,
    setImgModalShow,
    setErrorMassege
   } = props;

  const refCropper = React.createRef();
  const statuseSize = Array.isArray(imgModal.func)
  const [crop, setCrop] = useState({ x: 0, y: 0 })
  const [cropDesktop, setCropDesktop] = useState({ x: 0, y: 0 })
  const [rotation, setRotation] = useState(0)
  const [zoom, setZoom] = useState(1)
  const [zoomDesktop, setZoomDesktop] = useState(1)
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null)
  const [croppedAreaPixelsDesktop, setCroppedAreaPixelsDesktop] = useState(null)
  const [dogImg, setDogImg] = useState(imgModal.currBackground)
  const [event, setEvent] = useState(null);
  let initiIsDesktop=props.screenSize===0?false:true
  const [isDesktop, setIsDesktop] = useState(initiIsDesktop);

  useEffect(()=>{
    isDesktop? refCropper.current.scrollTop = 490:
    refCropper.current.scrollTop = 0;
    document.getElementById("wapper").scrollTop=0;

    return () => {
      document.getElementById("wapper").scrollTop=imgModal.scrollBack
    };

  });




 
  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels);
  }, [])

  const onCropCompleteDesktop = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixelsDesktop(croppedAreaPixels);
  }, [])

  const onClose = useCallback(() => {
    setDogImg(null);
    setImgModalShow(false);
  }, [])


  const changeCroppedImageByScreenSize = async (dogImg,
    croppedAreaPixels, rotation, event, func) => {
    const croppedImage = await getCroppedImg(
      dogImg,
      croppedAreaPixels,
      rotation,
      event
    )
    const result = changeImgBackground(croppedImage.file, croppedImage.url, imgModal.imgId, func, imgModal.type);
    return result
  }

  const showCroppedImage = useCallback(async () => {
    let result = ""
    try {
      if (statuseSize) {
        result = await changeCroppedImageByScreenSize(
          dogImg,
          croppedAreaPixels,
          rotation,
          event,
          imgModal.func[0]
        )
        result = await changeCroppedImageByScreenSize(
          dogImg,
          croppedAreaPixelsDesktop,
          rotation,
          event,
          imgModal.func[1]
        )
      }
      else {
        result = await changeCroppedImageByScreenSize(
          dogImg,
          croppedAreaPixels,
          rotation,
          event,
          imgModal.func
        )
      }
      if (result)
        setErrorMassege(result);
      else
        setErrorMassege("");
      if (!result || result === "")
        onClose()
    } catch (e) {
      console.error(e)
    }
  }, [croppedAreaPixels, croppedAreaPixelsDesktop, rotation])


  return (
    <>
      <input
        type="file" id="fileLoader" name="files" title="Load File"
        hidden
        accept="image/png, image/jpeg"
        onChange={(e) => {
          if (e.target.files[0]) {
            convertImgToBase64(e.target.files[0], setDogImg);
            setEvent(e.target.files[0])
          }
        }}
      />
      <div className="w-100 ">
        <div className="row d-flex justify-content-center mt-2">
          <h4>Add Image</h4>
        </div>
        <div className="row d-flex justify-content-center">
          {
            //desktop or mobile statuse
            statuseSize && dogImg ?
            
            <div className="d-flex justify-content-center">
              <Tooltip title="Mobile" arrow>
              <div className={!isDesktop?"selected mode-icon-upload-img":"mode-icon-upload-img"}>
                  <FontAwesomeIcon
                    icon={['fas', 'mobile-alt']}
                    onClick={() => {
                      refCropper.current.scrollTop = 0;
                      setIsDesktop(false);
                    }}
                    color="#3A405E"
                    className={!isDesktop?"selected-for-icon m-2 mb-0":"m-2 mb-0"}
                    >
                  </FontAwesomeIcon>
                </div>
              </Tooltip>
              <Tooltip title="Desktop" arrow>
                <div className={isDesktop?"selected mode-icon-upload-img":"mode-icon-upload-img"}>
                  <FontAwesomeIcon
                    title="Desktop mode"
                    icon={['fas', 'desktop']}
                    onClick={() => {
                      refCropper.current.scrollTop = 490;
                      setIsDesktop(true);
                    }}
                    className={isDesktop?"selected-for-icon m-2  mb-0":"m-2  mb-0"}
                    color="#3A405E"
                    />
                </div>
              </Tooltip>
            </div> : ""
          }</div>

        <div
          style={{
            height: dogImg && dogImg !== "" ? "450px" : "400px",
            overflow: statuseSize ? "auto" : "none",
          }}
          ref={refCropper}>
          <div>
            <CropperComponent
              img={dogImg}
              crop={crop}
              rotation={rotation}
              zoom={zoom}
              aspect={imgModal.aspectX / imgModal.aspectY} //first is width
              onCropChange={setCrop}
              onRotationChange={setRotation}
              onCropComplete={onCropComplete}
              onZoomChange={setZoom}
              cropShape={imgModal.cropShape}>
            </CropperComponent>
          </div>
          {
            statuseSize ?
              // desktop
              <CropperComponent
                img={dogImg}
                crop={cropDesktop}
                rotation={rotation}
                zoom={zoomDesktop}
                aspect={imgModal.aspectXDesktop / imgModal.aspectYDesktop} //first is width
                onCropChange={setCropDesktop}
                onRotationChange={setRotation}
                onCropComplete={onCropCompleteDesktop}
                onZoomChange={setZoomDesktop}
                cropShape={imgModal.cropShape}>
              </CropperComponent> : ""}
        </div>
        <label className="d-flex justify-content-center err-message">{imgModal.errorMassege}</label>
        <div className="d-flex justify-content-center">
          <button
            className={classes.cropButton}
            onClick={(() => openfileDialog("#fileLoader"))}
          >
            <FontAwesomeIcon icon={["fas", "cloud-upload-alt"]} className="m-1"></FontAwesomeIcon>
            Upload
            </button>
          <button
            onClick={showCroppedImage}
            className={`${classes.cropButton} ${classes.cropButtonWihgt}`}
          >
            Apply
        </button>
        </div>
      </div>
    </>
  )
}
const mapStateToProps = (state) => {
  return {
    imgModal: state.modal.imgModal,
    screenSize: state.card.screenSize,
  };
}
const mapDispatchToProps = (dispatch) => ({
  setImgModalShow: (status) => dispatch(actions.setImgModalShow(status)),

  setStatuseDesktopSize: (status) => dispatch(actions.setStatuseDesktopSize(status)),

  setErrorMassege: (massege) => dispatch(actions.setImgErrorMassege(massege))
})
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Demo))
