import React from 'react';
import CardHierOrder from './CardHook'
import BodyMpbile from './bodyMobile'
import BodyDesktop from './bodyDesktop'
import ErrorPage404 from '../ErrorPage404/ErrorPage404'


import { acceptCropperSize } from '../../BL/StyleCard'
import $ from 'jquery'
import './Card.css'
import '../../Css/default_card_designs.css'
import '../../Css/Templet/Templets.css'


const useMediaQuery = (query) => {
    const mediaMatch = window.matchMedia(query);
    return mediaMatch.matches;
};

const styles = {
    container: (isRowBased, cssObj) => ({
        height: cssObj.cover.height,
        backgroundImage: isRowBased ? cssObj.cover.backgroundImage : cssObj.coverDesktop.backgroundImage,
    })
};

class Card extends React.Component {

    

    render() {

       

        const {
            showState,
            cssObj,
            changeCoverStyle,
            setCoverDesktopcardStyle,
            changeProfileStyle,
            changeLogoStyle,
            setvCardStyleToggle,
            screenSize,
            card,
            setImgModal,
            preview,
            screenSizep,
        } = this.props;
        const isRowBased = useMediaQuery('(max-width: 750px)');


        return (
                showState && card.currentCard === "" ?
                <ErrorPage404/>:
                <>
                    {!showState||preview ?
                    <div
                        className={
                            screenSize === 0 ?showState?`${cssObj.cssObjTamplate.cover}`:
                            `${cssObj.cssObjTamplate.cover} camera-background` :
                            showState?`${cssObj.cssObjTamplate.coverDesktop}`:
                            `${cssObj.cssObjTamplate.coverDesktop} camera-background`
                        }
                        id={showState? "cover_prevwie" : "cover"}
                        title="Add Img"
                        style={screenSize === 0 ?
                            cssObj ? cssObj.cover ? cssObj.cover : null : null :
                            {
                                height: cssObj.cover.height,
                                backgroundImage: cssObj.coverDesktop.backgroundImage,
                            }}
                        onClick={!showState ? (e) => {
                            e.stopPropagation()
                            setImgModal({
                                show: true,
                                imgId: "#fileLoaderCover",
                                func: [changeCoverStyle, setCoverDesktopcardStyle],
                                type: 'div',
                                aspectX: 266,
                                aspectY: cssObj.cover ? 590 * (((parseFloat(cssObj.cover.height)) / 100)) : 3,
                                aspectXDesktop: $(window).width(),
                                aspectYDesktop: cssObj.cover ? $(window).height() * (((parseFloat(cssObj.cover.height)) / 100)) : 3,
                                cropShape: 'rect',
                                currBackground: cssObj.cover&&cssObj.cover.backgroundImage? cssObj.cover.backgroundImage:""
                            });
                            setvCardStyleToggle()
                        } : ""}
                    ></div> :
                        <div
                            className={isRowBased||(preview&&screenSize===0)? cssObj.cssObjTamplate.cover :
                                cssObj.cssObjTamplate.coverDesktop 
                            }
                            id="cover_prevwie"
                            style={styles.container(isRowBased, cssObj)}
                        ></div>
                    }
                    <div
                        className={showState? `${cssObj.cssObjTamplate.logo}`:`${cssObj.cssObjTamplate.logo} camera-background`}
                        id={showState || preview ? "logo_prevwie" : "logo"}
                        style={cssObj ? cssObj.logo ? cssObj.logo : null : null}
                        onClick={!showState ? (e) => {
                            e.stopPropagation()
                            setImgModal({
                                show: true,
                                imgId: "#fileLoaderLogo",
                                func: changeLogoStyle,
                                type: 'div',
                                aspectX: 160,
                                aspectY: 160,
                                cropShape: 'round',
                                currBackground: cssObj.logo&&cssObj.logo.backgroundImage? cssObj.logo.backgroundImage:""
                            });
                            setvCardStyleToggle()
                        } : ""}
                        title="Add Img"
                    ></div>
                    <div
                        style={{
                            width: "58px",
                            height: "58px",
                            top: `calc(-160px)`,
                            left: `calc(50% - ${"-20px"})`,
                            position: "relative", zIndex: 1
                        }}
                    >
                        <div>
                            {card.currentCard.vCardStyle.profileShow ? 
                            <div
                                className={showState?`${cssObj.cssObjTamplate.profile}`:`${cssObj.cssObjTamplate.profile} camera-background`}
                                id={showState || preview ? "profile_prevwie" : "profile"}
                                style={cssObj ? cssObj.profile ? cssObj.profile : null : null}
                                onClick={!showState ? (e) => {
                                    e.stopPropagation()
                                    setImgModal({
                                        show: true,
                                        imgId: "#fileLoaderProfile",
                                        func: changeProfileStyle,
                                        type: 'div',
                                        aspectX: 58,
                                        aspectY: 58,
                                        cropShape: 'round',
                                        currBackground: cssObj.profile&&cssObj.profile.backgroundImage? cssObj.profile.backgroundImage:""

                                    });
                                    setvCardStyleToggle()
                                } : ""}
                                title="Add Img"
                            >
                            </div> : ""}
                        </div>
                    </div>

                    {
                        !showState ? screenSize === 0 ?
                            <BodyMpbile {...this.props} ></BodyMpbile> :
                            <BodyDesktop {...this.props} ></BodyDesktop> :
                            preview ? screenSizep === 0 ?
                                <BodyMpbile {...this.props} ></BodyMpbile> :
                                <BodyDesktop {...this.props} ></BodyDesktop> :
                                <>
                                    <div className="d-md-none ">
                                        <BodyMpbile className="d-md-none " {...this.props}></BodyMpbile>
                                    </div>
                                    <div className="d-none d-md-block d-lg-block">
                                        <BodyDesktop {...this.props}></BodyDesktop>
                                    </div>
                                </>
                    }
                </>
        );
    }
}
export default CardHierOrder(Card);