import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function TextAlign(props) {
    const {  onClickAligh } = props;

    return (
        <>
            <div className="row">
                <div className="col-6 d-flex justify-content-start m-0 p-0">
                    <label className="title">alignment</label>
                </div>
                <div className="col-6 d-flex justify-content-around  m-0 p-0">

                    <FontAwesomeIcon onClick={() => { onClickAligh('left')}} icon={['fas', 'align-left']} color="#FFFF"></FontAwesomeIcon>


                    <FontAwesomeIcon onClick={() => { onClickAligh('center')}} icon={['fas', 'align-justify']} color="#FFFF"></FontAwesomeIcon>
                   

                    <FontAwesomeIcon onClick={() => { onClickAligh('right')}} icon={['fas', 'align-right']} color="#FFFF"></FontAwesomeIcon>

                </div>
            </div>
        </>
    );
}
