import produce from 'immer';
import createReducer from "./reducerUtils";


const initialState = {
    imgModal: {
        show: false,
        imgId: "",
        func: "",
        type: "div",
        aspectX: 3,
        aspectY: 3,
        aspectXDesktop: 5,
        aspectYDesktop: 2,
        cropShape: 'rect',
        currBackground: "",
        statuseDesktopSize: 0,//mobile 1=desktop
        errorMassege: "",
        scrollBack:0
    },

    cardNameModal: {
        show: false,
        errorMessage: "",
        cardIndex: -1
    }
};

const modal = {
    setImgModalShow(state, action) {
        state.imgModal.errorMassege = "";
        state.imgModal.show = action.payload;
    },
    setImgId(state, action) {
        state.imgModal.imgId = action.payload;
    },
    setFunc(state, action) {
        state.imgModal.func = action.payload;
    },
    setType(state, action) {
        state.imgModal.type = action.payload;
    },
    setAspect(state, action) {
        state.imgModal.aspectX = action.payload.x;
        state.imgModal.aspectY = action.payload.y;
        state.imgModal.cropShape = action.payload["cropShape"] ? action.payload.cropShape : 'rect'
    },
    setLastImage(state, action) {
        state.imgModal.lastImage = action.payload;
    },
    setStatuseDesktopSize(state, action) {
        state.imgModal.statuseDesktopSize = action.payload;
    },
    setImgErrorMassege(state, action) {
        state.imgModal.errorMassege = action.payload;
    },
    setImgModal(state, action) {
        const imgModalsDetails = action.payload,
            currBackground = imgModalsDetails.currBackground ?
                imgModalsDetails.currBackground.split("(")[1] : ""

        state.imgModal.imgId = imgModalsDetails.imgId;
        state.imgModal.func = imgModalsDetails.func
        state.imgModal.type = imgModalsDetails.type
        state.imgModal.aspectX = imgModalsDetails.aspectX
        state.imgModal.aspectY = imgModalsDetails.aspectY
        state.imgModal.aspectXDesktop = imgModalsDetails.aspectXDesktop
        state.imgModal.aspectYDesktop = imgModalsDetails.aspectYDesktop
        state.imgModal.cropShape = imgModalsDetails.cropShape
        state.imgModal.currBackground = 
            currBackground !== "" ? currBackground ?
            currBackground.split(")")[0]
            : imgModalsDetails.currBackground : "";
            
        state.imgModal.scrollBack = imgModalsDetails.scrollBack?imgModalsDetails.scrollBack:0;
        

        state.imgModal.show = imgModalsDetails.show

    },

    setBackgroundImg(state, action) {
        state.imgModal.currBackground = action.payload
    },

    setCardNameShow(state, action) {
        state.cardNameModal.show = action.payload.status;
        state.cardNameModal.errorMessage = "";
        state.cardNameModal.cardIndex = action.payload.index

    },

    checkUniqueCardName(state, action) {
        state.cardNameModal.cardName = action.payload.cardName
    },

    setErrorMassage(state, action) {
        state.cardNameModal.errorMessage = action.payload
    },


}

export default produce((state, action) => createReducer(state, action, modal), initialState);