import React, { Component } from "react";
import keys from '../../config/keys';

import { Switch, Route } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux';
import { Link } from "react-router-dom";
import { actions } from "../../Redux/actions/Action";
import { newCard } from '../../BL/Restarts';

import Nav from '../Nav'
import Alert from '@material-ui/lab/Alert';
import AlertDelete from '../alert/AlertDelete';
import ShareUrl from '../helpComponents/ShareUrl'
import SettingsIcon from '@material-ui/icons/Settings';
import Tooltip from '@material-ui/core/Tooltip';
import Popover from '@material-ui/core/Popover';

import '../../Css/NewWrap.css'




class Configurator extends Component {
    constructor(props) {
        super(props);
        this.state = {
            header:  `Card${props.currentCard.cardName || props.card.cards.length + 1}`,
            configurator: false,
            hiddenPhon: false,
            hiddenDesktop: false,
            openTooltip: false,
            checkedCheckbox: false,
            countPublishStatus: false,
            showPreview: false,
            anchorEl: null

        };

    }

    opendAndCloseTooltip = () => {

        this.setState({ openTooltip: !this.state.openTooltip })
    }
    checkedCheckbox = () => {
        this.setState({ checkedCheckbox: !this.state.checkedCheckbox })
    }
    setHeader = (header) => {
        this.setState({ header: header });
    }
    opendAndCloseAllCards = () => {
        this.setState({ configurator: !this.state.configurator })
    }
    countPublishStatus = () => {

        this.setState({ countPublishStatus: !this.state.countPublishStatus })
    }

    preview = () => {
        const screenSize = this.props.screenSize;
        let w = window.open(`${keys.API_URL_BASE_CLIENT}/preview/${screenSize}`, "_blank")
        w.card = this.props.card;
        w.agentDetails = this.props.agentDetails;
    }

    setAnchorEl = (event) => {
        this.setState({ anchorEl: event })
    }
    handleClick = (event) => {
        this.setAnchorEl(event.currentTarget);
    };

    handleClose = () => {
        this.setAnchorEl(null);
    };

    render() {
        const open = Boolean(this.state.anchorEl);
        const id = open ? 'simple-popover' : undefined;
        let cardName = this.props.currentCard.cardName
        return (
            <div className="container-fluid configurator_container"
                onClick={() => this.props.setImgModalShow(false)}>
                {/* header */}

                <header className="row configurator_header ">
                    {/* <div className="col-1 p-0"> */}
                    <div>
                        <div className="col-2 p-0"
                            aria-describedby={id}
                            variant="contained" color="primary"
                            onClick={this.handleClick}>
                            <Tooltip title="Settings" arrow>
                                <SettingsIcon style={{ color: "#FFF", cursor: "pointer" }}></SettingsIcon>
                            </Tooltip>
                        </div>
                        <Popover
                            id={id}
                            open={open}
                            anchorEl={this.state.anchorEl}
                            onClose={this.handleClose}
                            anchorOrigin={{
                                vertical: 'center',
                                horizontal: 'center',
                            }}
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                        >
                            <div >
                                <div className="pointer select"
                                    onClick={
                                        () => this.props.createNewCard(newCard())
                                    } >
                                    <Link to={`/${'admin'}/${this.props.userName}`}>
                                        <FontAwesomeIcon
                                            className="pr-1"
                                            icon={['fas', 'plus']}
                                            color="#8181A5"
                                        />
                                        <label className="pl-2 pr-3">
                                            Add New Card
                                        </label>
                                    </Link>
                                </div>

                                <div className="pointer select"
                                    onClick={() => {
                                        this.opendAndCloseAllCards();
                                        this.props.setImgModalShow(false)
                                    }}
                                >
                                    <Link to={this.state.configurator ?
                                        `/admin/${this.props.userName}` :
                                        `/admin/${this.props.userName}/AllCards`} >
                                        <FontAwesomeIcon
                                            className="pr-1"
                                            icon={['fas', 'border-all']}
                                            color="#8181A5"
                                        />
                                        <label className="pl-2 pr-3">
                                            All Cards
                                             </label>
                                    </Link>
                                </div>
                            </div>
                        </Popover>
                    </div>
                    {/* <Link className="col-2 p-0"
                        to={this.state.configurator ? `/admin/${this.props.userName}` : `/admin/${this.props.userName}/AllCards`} >
                        <Tooltip title="All cards" arrow>
                            <SettingsIcon style={{ color: "#FFF", cursor: "pointer" }}
                                onClick={() => { this.opendAndCloseAllCards(); this.props.setImgModalShow(false) }}>
                            </SettingsIcon></Tooltip>
                    </Link> */}
                    {/* </div> */}
                    <input
                        className="col configurator_header_title inputNoneBack text-transform-none"
                        onBlur={(e) => {
                            if (!e.currentTarget.contains(e.relatedTarget) && cardName !== e.target.value) {
                                this.props.checkUniqueCardName(
                                    {
                                        cardName: this.props.currentCard.cardName,
                                        index: this.props.card.currentCardIndex
                                    })
                            }
                        }}
                        value={this.props.currentCard.cardName || cardName}
                        onChange={(e) => {
                            this.props.editCurrentCardName(e.target.value);
                            this.props.ErrorCurrentMassageCardName("");
                        }}
                    ></input>
                    <div className="col-2  p-0 d-flex justify-content-end">
                        {/* <Tooltip title="Create Your Card" arrow>
                            <div className="pointer">
                            <Link to={`/${'admin'}/${this.props.userName}`}>
                                    <FontAwesomeIcon icon={['fas', 'plus']} color="#FFFF"
                                        onClick={
                                            () => this.props.createNewCard(newCard())
                                        } />
                                  </Link>
                            </div>
                        </Tooltip> */}
                        <Tooltip title={this.props.card.collaps.openAll ? "Open all" : "Close all"} arrow>
                            <div className="pointer pl-1">
                                {this.props.card.collaps.openAll ?
                                    <FontAwesomeIcon icon={['fas', 'chevron-down']} color="#FFFF"
                                        onClick={() => {
                                            this.props.setOpenAllTabsUnConfigurator(true);
                                        }} />
                                    : <FontAwesomeIcon icon={['fas', 'chevron-up']} color="#FFFF"
                                        onClick={() => {
                                            this.props.setOpenAllTabsUnConfigurator(false);
                                        }} />
                                }

                            </div>
                        </Tooltip>
                    </div>
                </header>
                <div className="row">
                    <label className="err-message">{
                        this.props.currentCard.errCardName
                    }</label>
                </div>


                {/* contaner you component*/}
                <div className="row configurator_main ">
                    <Switch>
                        <Route path="/">
                            <Nav></Nav>
                        </Route>
                    </Switch>
                </div>
                {this.props.alertStatusePublish === true || this.props.alertStatuseSucsses === true ? <AlertDelete index="1"></AlertDelete> : ""}
                {this.props.alertStatuseUpLodeImge === true || this.props.alertStatuseSucsses === true ? <AlertDelete index="1"></AlertDelete> : ""}
                {/* {this.state.openTooltip && this.props.publishStatus !== 1 ?
                    <Alert className="mb-1 row d-flex flex-column justify-content-center" icon={false} style={{ background: "transparent ", marginTop: "-50px", padding: "0px", position: "fixed", bottom: "40px", width: "220px" }}
                    >
                        <div style={{ background: "#716f76", color: "white" }}>switch to publish mode</div>
                        <div className={this.props.publishStatus === 2 ? "toolTipSave " : this.props.publishStatus === 0 ? "toolTipPublish" : ""}
                            onClick={(e) => {
                                e.preventDefault(); this.checkedCheckbox(); this.props.publishStatus === 0 ? this.props.setPublishStatus(2)
                                    : this.props.setPublishStatus(0)
                            }} >
                            <FormControlLabel
                                value="publish mode"
                                onClick={(e) => {
                                    e.preventDefault(); this.checkedCheckbox(); this.props.publishStatus === 0 ? this.props.setPublishStatus(2) : this.props.setPublishStatus(0)

                                }}
                                control={
                                    <Checkbox color="default" checked={this.props.publishStatus === 0 ? true : false} onChange={this.checkedCheckbox} />}
                            />
                             publish mode</div>

                    </Alert> : ""} */}
                {this.props.alertLink === true ?
                    <Alert className="mb-1 row d-flex flex-column justify-content-center" icon={false} style={{ background: "transparent ", marginTop: "-50px", padding: "0px", position: "fixed", bottom: "40px", width: "220px" }}
                    >
                        <div style={{ background: "#716f76", color: "white" }}>Url Adress</div>
                        <ShareUrl url={`${keys.API_URL_BASE_CLIENT}/${this.props.userName}/${cardName}`}></ShareUrl>

                    </Alert> : ""}

                {/* footer */}
                <footer className="row configurator_footer d-flex justify-content-center align-items-center">
                    <div className=" d-flex justify-content-center align-items-center" style={{ width: "200px" }}>

                        <button
                            className={this.props.publishStatus === 2 ? "savePublish rounded-pill" : this.props.publishStatus === 0 ? "publish rounded-pill m-1" : "upDatePublish rounded-pill m-1"}
                            onClick={() => {
                                this.props.publishStatus === 1 ?
                                    this.props.updateCard(false) :
                                    this.props.publishStatus === 0 && this.state.countPublishStatus !== true ?
                                        this.props.setalertStatusePublish(true) && this.countPublishStatus() :
                                        this.props.saveCard();
                                   

                            }}
                        >
                            {
                                this.props.publishStatus === 1 ?
                                    <FontAwesomeIcon
                                        icon={['fas', 'caret-up']}
                                        className="mr-2"
                                        color="#A66DFF"
                                    /> :
                                    this.props.publishStatus === 2 ?
                                        <FontAwesomeIcon
                                            icon='save'
                                            className="mr-2"
                                            color="#FFFF"
                                        /> :
                                        <FontAwesomeIcon
                                            icon='caret-right'
                                            className="mr-2"
                                            color="#FFFF"
                                        />
                            }
                            {
                                this.props.publishStatus === 2 ?
                                    "Save As" :
                                    this.props.publishStatus === 0 ?
                                        "Publish" : "Update"
                            }
                            {
                                this.props.publishStatus === 2 ?
                                    <FontAwesomeIcon
                                        icon={['fas', 'angle-down']}
                                        className="ml-2"
                                        color="#FFFF"
                                        onClick={e => { e.preventDefault(); e.stopPropagation(); this.opendAndCloseTooltip() }}
                                    /> : ""
                            }
                        </button>
                    </div>
                </footer>

            </div >
        );
    }
}

const mapStateToProps = (state) => {
    return {
        user: state.user,
        userId: state.user.userId,
        card: state.card,
        currentCard: state.card.currentCard,
        agentDetails: state.agentDetails.currentAgentForCard,
        publishStatus: state.card.publishStatus,
        userName: state.user.userName,
        alertStatusePublish: state.alert.alertStatusePublish,
        alertStatuseSucsses: state.alert.alertStatuseSucsses,
        screenSize: state.card.screenSize,
        alertLink: state.alert.alertLink,
        alertStatuseUpLodeImge: state.alert.alertStatuseUpLodeImge,

    };
}
const mapDispatchToProps = (dispatch) => ({

    setScreenSize: (status) => dispatch(actions.setScreenSize(status)),
    addNewCurrentCard: (preview) => { return dispatch(actions.saveCard(preview)) },
    updateCard: (preview) => dispatch(actions.updateCard(preview)),
    setPublishStatus: (status) => dispatch(actions.setPublishStatus(status)),
    setalertStatusePublish: (status) => dispatch(actions.setalertStatusePublish(status)),
    setalertStatuseSucsses: (status) => dispatch(actions.setalertStatuseSucsses(status)),
    setalertStatusePreview: (status) => dispatch(actions.setalertStatusePreview(status)),
    setalertLink: () => dispatch(actions.setalertLink()),
    createNewCard: (newCard) => dispatch(actions.setNewCard(newCard)),
    setCardNameShow: (status) => dispatch(actions.setCardNameShow(status)),
    setImgModalShow: (status) => dispatch(actions.setImgModalShow(status)),
    setOpenAllTabsUnConfigurator: (status) => dispatch(actions.setOpenAllTabsUnConfigurator(status)),
    checkUniqueCardName: (cardNameAndIndex) => dispatch(actions.checkUniqueCardName(cardNameAndIndex)),
    ErrorCurrentMassageCardName: (errMessage) => dispatch(actions.ErrorCurrentMassageCardName(errMessage)),
    setCurrentCardName: (cardName) => dispatch(actions.setCurrentCardName(cardName)),
    editCurrentCardName: (name) => dispatch(actions.editCurrentCardName(name)),
    saveCard:()=>dispatch(actions.saveCard())
})

export default connect(mapStateToProps, mapDispatchToProps)(Configurator);

