let files = {}, formData = new FormData()


export const setFile = (obj, isLIst = false) => {

    let key = Object.keys(obj)[0];
    if (isLIst) {
        if (!files.hasOwnProperty(key))
            files = { ...files, ...{ [key]: [] } }
        files[key].push(obj[key])
        return files[key].length - 1;
    }
    else
        files = { ...files, ...obj }
};



export const getFiles = () => {
    for (let key in files) {
        if (files.hasOwnProperty(key)) {
            if (Array.isArray(files[key]))
                files[key].forEach((item) => {
                    formData.append(key, item);
                })
            else
                formData.append(key, files[key]);
        }
    }
    return formData
}

export const getFileByKey = (key) => {
    return files[key];
}

export const resetFiles = () => {
    files = {};
    formData = new FormData();
}

const setFilesObj = (key, obj, newValue) => {
    if (typeof obj === 'object') {
        for (let item in obj) {
            if (item === key) {
                if (Array.isArray(obj[item])) {
                    obj[item].forEach((val, index) => {
                        let value = !Array.isArray(newValue) ? newValue : newValue[obj[item][index].indexFile]
                        if (value && obj[item][index].backgroundImage.startsWith("blob:http:"))
                            obj[item][index].backgroundImage = value.url;
                    });
                }
                else
                    obj[item] = { ...obj[item], ...{ backgroundImage: `url(${newValue.url})` } };
                return;
            }
            setFilesObj(key, obj[item], newValue)
        };
    }
}

export const setDataForFormData = (data, obj) => {
    for (let key in data) {
        if (data.hasOwnProperty(key)) {
            setFilesObj(key, obj, data[key])
        }
    }
}