import React from 'react';
import { withRouter } from "react-router";
import Card from './Card'
import CardMobile from './CardMobile'
import CardTablet from './CardTablet'



// e 1
export default withRouter(function CardPreview(props) {
const  screenSize =parseInt(props.match.params["screenSize"]?props.match.params.screenSize:"");
    return (
        <>
         {
            screenSize===0?
            <CardMobile  screenSizep={screenSize} preview={true}></CardMobile>:
            this.props.screenSize === 1 ?
            <Card screenSizep={screenSize} showState={true} preview={true} /> :
            <CardTablet screenSizep={screenSize} showState={true} preview={true} />

        }
        </>
    );
})
