import React from 'react';
import { OnOff } from '../helpComponents/OnOff'
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { makeStyles } from '@material-ui/core/styles';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Slider from '@material-ui/core/Slider';
import '../../Css/SelecComponent.css'

function mapStateToProps(state) {

    return {
        showThumbnails: state.gallerySlide.currentGallery.slide.showThumbnails,
        showPlayButton: state.gallerySlide.currentGallery.slide.showPlayButton,
        showBullets: state.gallerySlide.currentGallery.slide.showBullets,
        showNav: state.gallerySlide.currentGallery.slide.showNav,
        slideDuration: state.gallerySlide.currentGallery.slide.slideDuration,
        slideInterval: state.gallerySlide.currentGallery.slide.slideInterval,
        animation: state.gallerySlide.currentGallery.slide.animation,
        timeout: state.gallerySlide.currentGallery.slide.timeout,
        checkedRadio: state.gallerySlide.currentGallery.slide.checkedRadio,
    };
}
function mapDispatchToProps(dispatch) {
    return {

        setShowThumbnails: (cheaked, socialMediaType) => dispatch(actions.setShowThumbnails(cheaked)),
        setAutoPlay: (cheaked, socialMediaType) => dispatch(actions.setAutoPlay(cheaked)),
        setShowBullets: (cheaked, socialMediaType) => dispatch(actions.setShowBullets(cheaked)),
        setShowNavigation: (cheaked, socialMediaType) => dispatch(actions.setShowNavigation(cheaked)),
        changeSlideDuration: (name) => dispatch(actions.setSlideDuration(name)),
        changeSlideInterval: (name) => dispatch(actions.setslideInterval(name)),
        setAnimation: (name) => dispatch(actions.setAnimation(name)),
        setTimeout: (name) => dispatch(actions.setTimeout(name)),
        setCheckedRadio: (cheaked, socialMediaType) => dispatch(actions.setCheckedRadio(cheaked)),



    }
};

export default connect(mapStateToProps, mapDispatchToProps)(function NavSlideGallery(props) {
    const { 
        setAutoPlay, 
        showPlayButton,
        setShowBullets,
        showBullets, 
        animation,
        setAnimation,
        setTimeout,
        timeout
     } = props;


    const useStyles = makeStyles((theme) => ({
        root: {
            '& .MuiInput-underline:after': {
                borderBottomColor: '#75798E',
            },
            '& .MuiInput-underline:before': {
                borderBottomColor: '#75798E',
            },
            '& .MuiInput-underline:hover:not(.MuiInput-disabled-23):not(.MuiInput-focused-22):not(.MuiInput-error-25):before':
            {
                borderBottom: "1px solid #75798E !important"
            },
            '& .MuiSvgIcon-root':
            {
                color: "#75798E"
            }
        },
        textFiled:
        {
            '& .MuiInputBase-input':
            {
                color: '#CFD1D9',
                fontSize: '0.8em'
            },
        },

    }));
    const classes = useStyles();


    return (
        <div className="mt-2 " >

            <div className="row input_footer">
                <div className="col-8">
                    <label id="aboutMe" className="title">Play Auto</label>
                </div>
                <div className="col-4 p-0 d-flex justify-content-end">
                    <OnOff
                        title={"PlayButton"}
                        cheak={showPlayButton}
                        onChangeOnOff={setAutoPlay}
                        type={"fullName"}
                        idElement="#PlayButton"

                    ></OnOff>
                </div>
            </div>
            <div className="row input_footer">
                <div className="col-8">
                    <label id="aboutMe" className="title">Bullets</label>
                </div>
                <div className="col-4 p-0 d-flex justify-content-end">
                    <OnOff
                        title={"showBullets"}
                        cheak={showBullets}
                        onChangeOnOff={setShowBullets}
                        type={"fullName"}
                        idElement="#showBullets"

                    ></OnOff>
                </div>
            </div>

            {/* <div className="row input_footer">
                <div className="col-8">
                    <label id="aboutMe" className="title">Navigation</label>
                </div>
                <div className="col-4 p-0 d-flex justify-content-end">
                    <OnOff
                        title={"showNavigation"}
                        cheak={showNav}
                        onChangeOnOff={setShowNavigation}
                        type={"fullName"}
                        idElement="#showNavigation"
                    ></OnOff>
                </div>
            </div> */}

            <div className="row">
                <RadioGroup row aria-label="position" name="position" defaultValue="top">
                    <FormControlLabel
                        onChange={(e) => setAnimation(e.target.checked ? 'slide' : 'fade')}
                        control={<Radio color="primary" />} label="Slide" className=" title"
                        checked={animation === 'slide' ? true : false} />
                    <FormControlLabel
                        onChange={(e) => setAnimation(e.target.checked ? 'fade' : 'slide')} value="fade" control={<Radio color="primary" />} label="Fade" className=" title"
                        checked={animation === 'fade' ? true : false} />
                </RadioGroup>
            </div>

            <div className="row">
                <label className="title">timeout</label>
            </div>
            <div className="row mt-2">
                <div
                    className="col m=0  title d-flex align-items-end slider-padding"
                >
                    <Slider
                        style={{color: 'blue'}}
                        onChangeCommitted={(event,value) => setTimeout(value)}
                        value={timeout}
                        defaultValue={timeout}
                        //  getAriaValueText={valuetext}
                        aria-labelledby="continuous-slider"
                        valueLabelDisplay="auto"
                        step={100}
                        min={100}
                        max={2000}
                    />
                </div>
                <input type="number" className="col-3 m-0 p-0 input d-flex align-items-start"
                    value={timeout}
                    onChange={(event) => setTimeout(event.target.value)}

                ></input>
            </div>
        </div>



    );
})


