import React, { Component } from "react";
import Contact from './helpComponents/Contact'
import { actions } from '../Redux/actions/Action';
import { connect } from 'react-redux';
import VcardStyleSocialMedia from './VcardStyleSocialMedia'
import AgentDetail from './helpComponents/AgentDetail'
import '../Css/ComponentNav.css'
import '../Css/SocialMedia.css'
import { OnOff } from './helpComponents/OnOff'



class SocialMedia extends Component {
    constructor(props) {
        super(props);
        this.state = {
            disabledContactCheaked: true
        };

    }

    changeIconCheaked = (cheaked, socialMediaType) => {
        this.props.setsocialMediaUrlShow(socialMediaType, cheaked)
    }

    render() {

     
        return (
            <div className="container-fluid mt-2">
                <div className="row mb-3">
                    <AgentDetail
                        header="Mobile Number"
                        title={this.props.agentDetails.mobile}
                        placeholder="Enter your mobile number"
                        onChange={this.props.changeMobile}
                        typeInput="tel"
                        name="mobile"
                        error={this.props.validation.errors.mobile}
                        validationOnChange={this.props.validationOnChange}
                        hiddenOnOff={false}
                        
                    ></AgentDetail>
                </div>
                <div className="row mb-3">

                    {/* <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            idElement="footerShow"
                            cheak={this.props.vCardStyle.footerShow}
                            onChangeOnOff={this.props.setFooterShow}
                        ></OnOff>
                    </div> */}

                    <AgentDetail
                        header="Personal Email"
                        title={this.props.agentDetails.personalEmail}
                        placeholder="Enter your personal email"
                        onChange={this.props.changePersonalEmail}
                        typeInput="email"
                        name="email"
                        error={this.props.validation.errors.email}
                        validationOnChange={this.props.validationOnChange}
                        hiddenOnOff={true}
                        // idElement="footerShow"
                        cheak={this.props.vCardStyle.footerShow}
                        onChangeOnOff={this.props.setFooterShow}

                    ></AgentDetail>

                </div>

                {
                    this.props.socialMedias.map((socialMedia, i) => {
                        return <div className="row mb-3" key={i}>
                            <Contact
                                id={socialMedia.name}
                                key={i}
                                index={i}
                                title={socialMedia.name}
                                onChangeTitle={this.props.setSocialMediaTitle}
                                // icon={this.props.socialMediaIcons[socialMedia.name]}
                                icon={socialMedia.icon}
                                url={socialMedia.url}
                                cheak={socialMedia.show}
                                onChange={this.props.attachUrlSocialMedia}
                                onChangeOnOff={this.changeIconCheaked}
                                onChangeIconSocialMedia={this.props.setsocialMediaIcon}
                                validationOnChange={this.props.validationOnChange}
                                typeInput="text"
                                name={socialMedia.name}
                                setWhatsAppWelcomMassage={this.props.setWhatsAppWelcomMassage}
                                error={this.props.validation.errors[socialMedia.name]}
                            ></Contact>
                        </div>

                    })
                }

                <div>
                    <VcardStyleSocialMedia></VcardStyleSocialMedia>
                </div>
            </div >
        );
    }
}
const mapStateToProps = (state) => {
    return {
        card: state.card.currentCard.socialMedia,
        socialMedias: state.card.currentCard.socialMedias,
        socialMediaIcons: state.card.socialMediaIcons,
        vCardStyle: state.card.currentCard.vCardStyle,
        validation: state.validation,
        agentDetails: state.agentDetails.currentAgentForCard,
        user:state.user
    };
}

const mapDispatchToProps = (dispatch) => ({
    changeIconCheaked: (cheaked, index) => dispatch(actions.setsocialIconCheaked({ index: index, value: cheaked })),

    attachUrlSocialMedia: (url, index) => dispatch(actions.setsocialMediaUrl({ index: index, url: url })),

    validationOnChange: (event) => dispatch(actions.handleChange(event)),

    setsocialMediaIcon: (icon, index) => dispatch(actions.setsocialMediaIcon({ index: index, icon: icon })),

    setSocialMediaTitle: (title, index) => dispatch(actions.setSocialMediaTitle({ index: index, name: title })),

    setsocialMediaUrlShow: (index, status = null) => dispatch(actions.setsocialMediaUrlShow({ index: index, status: status })),
    
    setVcardStyle: (name) => dispatch(actions.setVcardStyle(name)),

    changeMobile: (mobile) => dispatch(actions.setMobile(mobile)),

    changePersonalEmail: (email) => dispatch(actions.setPersonalemail(email)),

    setWhatsAppWelcomMassage: (message) => dispatch(actions.setWhatsAppWelcomMassage(message)),
    setFooterShow:(status) => dispatch(actions.setFooterShow(status)),

})
export default connect(mapStateToProps, mapDispatchToProps)(SocialMedia);