import React from 'react';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { OnOff } from '../helpComponents/OnOff'
import StyleInputCard from '../helpComponents/StyleInputCard'
import IconShapStyle from '../helpComponents/IconShapStyle'
import AgentDetail from '../helpComponents/AgentDetail'
import TextAlign from '../../components/textAlign'
import './Reveiws.css'

function mapStateToProps(state) {

  return {
    reveiw: state.reveiws.currentReviews
  };
}
function mapDispatchToProps(dispatch) {
  return {
    changeRoleReveiwsShow: (cheaked, socialMediaType) => dispatch(actions.setRoleReveiwsShow(cheaked)),
    setButtonReveiws: (name) => dispatch(actions.setButtonReveiws(name)),
    setFullNameReveiws: (name) => dispatch(actions.setFullNameReveiws(name)),
    setRuleReveiws: (name) => dispatch(actions.setRuleReveiws(name)),
    setMessageReveiws: (name) => dispatch(actions.setMessageReveiws(name)),
    setReveiwsTitle: (title) => dispatch(actions.setReveiwsTitle(title)),
    setReveiwShow: (stateShow) => dispatch(actions.setReveiwShow(stateShow)),
    setCoverReveiwsStyle: (style) => dispatch(actions.setCoverReveiwsStyle(style)),
    setBackgroundReveiwsStyle: (style) => dispatch(actions.setBackgroundReveiwsStyle(style)),
    setColorFullNameReveiws: (style) => dispatch(actions.setColorFullNameReveiws(style)),
    setImgModal: (obj) => dispatch(actions.setImgModal(obj)),
    addNewReveiws: (name) => dispatch(actions.addNewReveiws(name)),
    setstyleColorArrowReviews:(color) => dispatch(actions.setstyleColorArrowReviews(color))


  }
};

export default connect(mapStateToProps, mapDispatchToProps)(function ReveiwsNav(props) {
  const {
    reveiw,
    addNewReveiws,
    setFullNameReveiws,
    changeRoleReveiwsShow,
    setColorFullNameReveiws,
    setRuleReveiws,
    setMessageReveiws,
    setReveiwsTitle,
    setReveiwShow,
    setCoverReveiwsStyle,
    setstyleColorArrowReviews,
    setBackgroundReveiwsStyle,
    setImgModal
  } = props;


  return (
    <>
      <div className="container-fluid">

        <div className="row ">
          <label className="title">Reviews Settings</label>
        </div>
        <div className="row ">
          <input className="col-8 p-0 input"
            value={reveiw.titleReveiws}
            onChange={(e) => { setReveiwsTitle(e.target.value) }}
          ></input>
          <div className="col-4 p-0 d-flex justify-content-end">
            <OnOff
              idElement="ReveiwsShow"
              cheak={reveiw.reveiwShow}
              onChangeOnOff={setReveiwShow}
              type={"ReveiwsShow"}
            ></OnOff>
          </div>

        </div>

        <div className="row title mt-2 ">recommender image or logo</div>
        <div className="row mb-3 ImageLoadReveiws"
          id="navCaver"
          onClick={(e) => {
            e.stopPropagation()
            setImgModal({
              show: true,
              imgId: "#fileLoaderReveiws",
              func: setCoverReveiwsStyle,
              type: 'div',
              aspectX: 40,
              aspectY: 40,
              cropShape: 'round',
              scrollBack:700,
              currBackground: reveiw.reveiwsList ? reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsStyle.profile_reveiws.backgroundImage : ""
            });

          }}
          style={{ background: reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsStyle.profile_reveiws.backgroundImage, backgroundSize: "cover", }}
        ></div>
        <div className="row mb-3">
          <AgentDetail
            header="recommend name"
            title={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsDetails.full_name}
            cheak={reveiw.roleShow}
            onChange={setFullNameReveiws}
            onChangeOnOff={changeRoleReveiwsShow}
            hiddenOnOff={false}
            type={"fullName"}
            idElement="#showFullNameReveiws"
          ></AgentDetail>
        </div>
        <div className="row mb-3">
          <AgentDetail
            header="profession"
            title={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsDetails.role}
            cheak={reveiw.roleShow}
            onChange={setRuleReveiws}
            onChangeOnOff={changeRoleReveiwsShow}
            hiddenOnOff={false}
            type={"fullName"}
            idElement="#showFullNameReveiws"
          ></AgentDetail>
        </div>
        <div className="row mt-3">
          <StyleInputCard
            title="Background title"
            background={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsStyle.input_card_fullName.color}
            onchangeColor={setColorFullNameReveiws}
            color={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsStyle.input_card_fullName.color}
          > </StyleInputCard>
        </div>
        <div className="row mt-2">
          <StyleInputCard
            title="Background Reveiws"
            background={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsStyle.bodyReveiws.background}
            onchangeColor={setBackgroundReveiwsStyle}
            color={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsStyle.bodyReveiws.background}
          > </StyleInputCard>
        </div>
        <div className="row mt-2">
          <StyleInputCard
            title="Color Nav Reveiws"
            background={reveiw.styleColorArrow}
            onchangeColor={setstyleColorArrowReviews}
            color={reveiw.styleColorArrow}
          > </StyleInputCard>
        </div>

      </div>


      <div className="col container">
        <div className="row title mb-2">recommendation Content</div>
        <div className="row d-flex flex-column textReveiws">
          <div className="form-group p-0 m-0" >
            <textarea className="form-control p-0  m-0 rounded textAgentDetailsTextArea " id="exampleFormControlTextarea2"
              onChange={(e) => { setMessageReveiws(e.target.value); }}
              value={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsDetails.message_reveiws}
              placeholder={"Message..."}
              style={{
                height: "150px"
              }}
            >
              {reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsDetails.message_reveiws}
            </textarea>
          </div>
        </div>
        {
          <div className="d-flex justify-content-center mt-2">
            <button style={{
              width: "200px",
              color: "rgb(255, 255, 255)",
              background: "rgb(221, 182, 228)",
              /* border-radius: 10px; */
              textAlign: "center",
              height: "100%"
            }} onClick={(e) => { addNewReveiws() }}>Add New Reveiw +</button>
          </div>
        }
      </div>

    </>
  );

});
