import React from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import SocialMediaCard from './SocialMediaCard'
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import Typography from '@material-ui/core/Typography';
import Reveiws from '../Reveiws/Reveiws'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ImageGridList from '../Gallery/GridGallery'
import SlideGallery from '../Gallery/SlideGallery'
import VideoCard from '../Video/VideoCard'
import './Card.css'
import '../../Css/default_card_designs.css'
import '../TamplateCard/tamplate.css'

class BodyMpbile extends React.Component {

    scrolToFotter = () => {
        if (this.props.preview) {
            document.getElementsByClassName("mobile")[1].scrollBy(0, 1000);
        }
        else {
            if (document.getElementsByClassName("mobile").length > 0)
                document.getElementsByClassName("mobile")[0].scrollBy(0, 1000);
            else if (document.getElementById("View"))
                document.getElementById("View").scrollBy(0, 1000);
        }
        this.props.setFooterStyleToggle();
    }

    handleChange = (panel) => (event, isExpanded) => {
        this.props.setExpanded({ isExpanded: isExpanded, panel: panel });
    };
    // bodyCardMobile
    render() {
        let num = 10, rows = [];
        const {
            showState,
            cssObj,
            agentDetails,
            card,
            gallery,
            changeFullName,
            changeRule,
            setAgentDetailsToggle,
            setAbouttitle,
            setSocialMediaToggle,
            setvCardStyleToggle,
            setFooterStyleToggle,
            setGalleryStyleToggle,
            setvideoStyleToggle,
            setReveiwsTitle,
            preview,
            setReveiwsToggle,
            setGallerytitle,
            setVideoTitle,
            reveiw,
            user,
            video,
        } = this.props;
        return (
            <>
                <div
                    className="row defult_card_body_modern "
                    style={
                        cssObj ? cssObj.bodyCardMobile.body_card ?
                            cssObj.bodyCardMobile.body_card : null : null
                    }
                    id={showState && !preview ? "body_mobile_preview" : "body_mobile"}
                    onClick={(e) => { e.stopPropagation(); setvCardStyleToggle() }}
                    title={showState ? "" : "Change body color"}
                >
                    <div
                        className="col d-flex flex-column p-0"
                        style={{
                            background: cssObj.bodyCardMobile.body_card.background,
                            marginTop: cssObj.bodyCardMobile.body_card.body_content_top
                        }}
                    >
                        {
                            !showState ?
                                <>
                                    <input
                                        autoComplete="new-password"
                                        className="row ml-2 mr-2 input_card input-placholder"
                                        value={agentDetails.fullName}
                                        placeholder="Enter your name"

                                        onChange={(e) => { changeFullName(e.target.value) }}
                                        onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}
                                        hidden={!card.currentCard.vCardStyle.fullNameShow}
                                        style={
                                            cssObj ? cssObj.bodyCardMobile.input_card_fullName ?
                                                cssObj.bodyCardMobile.input_card_fullName : null : null
                                        }
                                        title={showState ? "" : "Change name"}
                                        id={showState ? "fullName_p" : "fullName"}
                                    />
                                    <input
                                        className="row ml-2 mr-2 input_card input-placholder"
                                        placeholder="Enter your role"
                                        value={agentDetails.rule}
                                        onChange={(e) => { changeRule(e.target.value) }}
                                        onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}
                                        hidden={!card.currentCard.vCardStyle.ruleShow}
                                        style={
                                            cssObj ? cssObj.bodyCardMobile.input_card_rule ?
                                                cssObj.bodyCardMobile.input_card_rule : null : null
                                        }
                                        title={showState ? "" : "Change role"}
                                        id={showState ? "role_p" : "role"}
                                    />
                                </> :
                                <>
                                    <label
                                        className="row  p-0 input_card"
                                        style={
                                            cssObj ? cssObj.bodyCardMobile.input_card_fullName ?
                                                cssObj.bodyCardMobile.input_card_fullName : null : null
                                        }
                                    >
                                        {agentDetails.fullName !== "" ? agentDetails.fullName : ""}
                                    </label>
                                    {
                                        agentDetails.rule !== "" ?
                                            <label
                                                className="row  p-0 input_card"
                                                hidden={!card.currentCard.vCardStyle.ruleShow}
                                                style={
                                                    cssObj ? cssObj.bodyCardMobile.input_card_rule ?
                                                        cssObj.bodyCardMobile.input_card_rule : null : null
                                                }
                                            >
                                                {agentDetails.rule}
                                            </label> : ""
                                    }
                                </>
                        }
                        <div
                            className={
                                cssObj ? cssObj.bodyCardMobile.socialMediaSpace.bodySpaceCss ?
                                    cssObj.bodyCardMobile.socialMediaSpace.bodySpaceCss : "" : ""
                            }
                            style={
                                cssObj ? cssObj.socialMediaSpace ?
                                    cssObj.socialMediaSpace : null : null
                            }
                        >
                            <SocialMediaCard
                                css={
                                    cssObj ? cssObj.bodyCardMobile.socialMediaSpace ?
                                        cssObj.bodyCardMobile.socialMediaSpace.css : "col-4 mt-4" : "col-4 mt-4"
                                }
                                showState={showState}
                                onClickFunc={!showState ? setSocialMediaToggle : null}
                                styleIcon={
                                    cssObj ? cssObj.bodyCardMobile.socialMedia ?
                                        cssObj.bodyCardMobile.socialMedia : null : null
                                }
                                title={"Call me"}
                                icon={"FaPhoneAlt"}
                                url={`tel:+${card.currentCard.agentDetails.mobile}`}
                            />

                            {
                                card.currentCard.vCardStyle.footerShow &&

                                <SocialMediaCard
                                    id="mail"
                                    css={
                                        cssObj ? cssObj.bodyCardMobile.socialMediaSpace ?
                                            cssObj.bodyCardMobile.socialMediaSpace.css :
                                            "col-4 mt-4" : "col-4 mt-4"
                                    }
                                    showState={showState}
                                    onClickFunc={!showState ? setSocialMediaToggle : null}
                                    styleIcon={
                                        cssObj ? cssObj.bodyCardMobile.socialMedia ?
                                            cssObj.bodyCardMobile.socialMedia : null : null
                                    }
                                    title={"Mail"}
                                    icon={"FaEnvelope"}
                                    url={''}
                                    setFooterSendEmailShowMobile={this.scrolToFotter}
                                />
                            }
                            {
                                card.currentCard.socialMedias.map((socialMedia, i) => {
                                    if (socialMedia.show === true) {
                                        num -= 1;
                                        return (
                                            <SocialMediaCard
                                                key={i}
                                                showState={showState}
                                                css={
                                                    cssObj ? cssObj.bodyCardMobile.socialMediaSpace && i > 1 ?
                                                        cssObj.bodyCardMobile.socialMediaSpace.css :
                                                        "col-4 mt-4" : "col-4 mt-4"
                                                }
                                                onClickFunc={!showState ? setSocialMediaToggle : null}
                                                styleIcon={
                                                    cssObj ? cssObj.bodyCardMobile.socialMedia ? cssObj.bodyCardMobile.socialMedia : null : null
                                                }
                                                title={socialMedia.name}
                                                icon={socialMedia.icon}
                                                url={socialMedia.url}
                                            />
                                        )
                                    }
                                    return "";
                                })
                            }

                            {/* {num > 0 && !showState ?
                                <SocialMediaCard
                                    css={
                                            cssObj ? cssObj.bodyCardMobile.socialMediaSpace ?
                                            cssObj.bodyCardMobile.socialMediaSpace.css :
                                            "col-4 mt-2" : "col-4 mt-2"
                                    }
                                    showState={showState}
                                    onClickFunc={!showState ? setSocialMediaToggle : null}
                                    styleIcon={
                                        cssObj ? cssObj.bodyCardMobile.socialMedia ?
                                        cssObj.bodyCardMobile.socialMedia : null : null
                                    }
                                    title={"Plus"}
                                    icon={"FaPlus"}
                                    url={``}
                                ></SocialMediaCard>
                                : ""} */}
                            {rows.map((val) => { return val; })}
                        </div>

                        {/* abouts */}

                        <div className="p-1 mt-3"
                            style={{ width: "100%" }}
                            onClick={(e) => {
                                e.stopPropagation();
                                setAgentDetailsToggle();
                            }}>
                            {card.currentCard.vCardStyle.aboutShow ?
                                showState && agentDetails.aboutConnect === "" ? "" : <Accordion
                                    style={{
                                        letterSpacing: cssObj.bodyCardMobile.input_about_card.letterSpacing,
                                        color: cssObj.bodyCardMobile.styleTitleBody.color,
                                        background: cssObj.bodyCardMobile.input_about_card.background,
                                        border: cssObj.bodyCardMobile.input_about_card.border,
                                        outline: cssObj.bodyCardMobile.input_about_card.outline,
                                        opacity: cssObj.bodyCardMobile.input_about_card.opacity,
                                        borderBottom: cssObj.bodyCardMobile.input_about_card.borderBottom,
                                        margin: cssObj.bodyCardMobile.input_about_card.margin,

                                    }}
                                    expanded={card.expanded === 'about'}
                                    onChange={this.handleChange('about')}>
                                    <AccordionSummary
                                        className={cssObj.bodyCardMobile.styleTitleBody.textAlign  === "right" ? "direction" : ""}
                                        expandIcon={
                                            <ExpandMoreIcon
                                                style={{
                                                    color: cssObj.bodyCardMobile.styleTitleBody.color

                                                }}
                                            />
                                        }
                                        aria-controls="aboutbh-content"
                                        id="aboutbh-header"
                                    >
                                        <Typography>
                                            {showState ? agentDetails.aboutTitle !== "" ? agentDetails.aboutTitle : "About my business" :
                                                <input
                                                    style={cssObj.bodyCardMobile.styleTitleBody}
                                                    className={cssObj.cssObjTamplate.input_about_templet}
                                                    onChange={(e) => { setAbouttitle(e.target.value) }}
                                                    title={showState ? "" : "Change about title"}
                                                    value={agentDetails.aboutTitle !== "" ? agentDetails.aboutTitle : "About my business"}
                                                    placeholder="About my business"
                                                    id={showState ? "about_p" : "about"}
                                                ></input>}
                                        </Typography>
                                    </AccordionSummary>
                                    <AccordionDetails className="rounded" style={{ background: "transperent", color: "black" }}>
                                        <Typography style={{ width: "100%" }}>
                                            {!showState ? <textarea
                                                title="Write about yourself or your business"
                                                className="about_card about_card-edit"
                                                onChange={(e) => { e.stopPropagation(); this.props.setAboutConnect(e.target.value) }}
                                                value={agentDetails.aboutConnect !== "" ?
                                                    agentDetails.aboutConnect
                                                    : ""}
                                                style={{
                                                    color: cssObj.bodyCardMobile.styleTitleBody.color,
                                                    textAlign: cssObj.bodyCardMobile.styleTitleBody.textAlign,
                                                }}
                                                placeholder="Tell about your business..."
                                            // placeholder={"I'm a product designer based in Tel-Aviv with experience in delivering end-to-end UX/UI design for software products."}
                                            >
                                            </textarea > : this.props.agentDetails.aboutConnect !== "" ?
                                                    <div className="about_card"
                                                        style={{
                                                            color: cssObj.bodyCardMobile.styleTitleBody.color,
                                                            textAlign: cssObj.bodyCardMobile.styleTitleBody.textAlign,
                                                        }}
                                                    > {this.props.agentDetails.aboutConnect}
                                                    </div> : ""}
                                        </Typography>
                                    </AccordionDetails>
                                </Accordion> : ""}

                            {/* Gallery */}
                            {
                                gallery && gallery.galleryShow ?
                                    // showState&& gallery.galleries.length===0?"":
                                    <Accordion
                                        style={{
                                            letterSpacing: cssObj.bodyCardMobile.input_about_card.letterSpacing,
                                            color: cssObj.bodyCardMobile.styleTitleBody.color,
                                            background: cssObj.bodyCardMobile.input_about_card.background,
                                            border: cssObj.bodyCardMobile.input_about_card.border,
                                            outline: cssObj.bodyCardMobile.input_about_card.outline,
                                            opacity: cssObj.bodyCardMobile.input_about_card.opacity,
                                            borderBottom: cssObj.bodyCardMobile.input_about_card.borderBottom,
                                            margin: cssObj.bodyCardMobile.input_about_card.margin,
                                        }}
                                        expanded={card.expanded === 'gallery'}
                                        onChange={this.handleChange('gallery')}
                                        onClick={(e) => { e.stopPropagation(); setGalleryStyleToggle() }}
                                    >
                                        <AccordionSummary
                                            className={cssObj.bodyCardMobile.styleTitleBody.textAlign  === "right" ? "direction" : ""}
                                            expandIcon={
                                                <ExpandMoreIcon
                                                    style={{ color: cssObj.bodyCardMobile.styleTitleBody.color }}
                                                />
                                            }
                                            aria-controls="gallerybh-content"
                                            id="gallerybh-header"
                                        >
                                            <Typography>
                                                {
                                                    showState ? agentDetails.aboutTitle !== "" ?
                                                        agentDetails.aboutTitle : "Gallery" :
                                                        <input
                                                            style={cssObj.bodyCardMobile.styleTitleBody}
                                                            className={cssObj.cssObjTamplate.input_about_templet}
                                                            onChange={(e) => { setGallerytitle(e.target.value) }}
                                                            title={showState ? "" : "Change Gallery title"}
                                                            value={gallery.title !== "" ? gallery.title : ""}
                                                            placeholder="Gallery"
                                                            id={showState ? "about_p" : "about"}
                                                        />
                                                }
                                            </Typography>
                                        </AccordionSummary>

                                        <AccordionDetails
                                            className="rounded"
                                            style={{ background: "transperent", color: "black", width: "100%" }}
                                            onClick={!showState ? () => {
                                                this.props.setImgModal({
                                                    show: true,
                                                    imgId: "#fileLoaderGallery",
                                                    func: this.props.addImgGallery,
                                                    type: 'img',
                                                    aspectX: gallery.state === "grid" ? (1 / gallery.grid.cols) * 700 : 246,
                                                    aspectY: gallery.state === "grid" ? gallery.grid.cellHeightDesktop : 150,
                                                    cropShape: 'rect',
                                                })
                                            } : ""}
                                        >
                                            <Typography component={'span'} style={{ width: "100%" }}>
                                                <div className="row mt-3  d-flex flex-column p-2">
                                                    {
                                                        gallery.state === 'grid' ?
                                                            <ImageGridList {...this.props}></ImageGridList> :
                                                            <SlideGallery {...this.props} ></SlideGallery>
                                                    }
                                                </div>
                                            </Typography>
                                        </AccordionDetails>
                                    </Accordion> : ""
                            }

                            {/* Reveiws */}
                            {
                                reveiw.reveiwShow ?
                                    showState && reveiw.reveiwsList.length === 1
                                        && reveiw.reveiwsList[0].reveiwsDetails.full_name === "" ? "" :
                                        <Accordion
                                            style={{
                                                letterSpacing: cssObj.bodyCardMobile.input_about_card.letterSpacing,
                                                color: cssObj.bodyCardMobile.styleTitleBody.color,
                                                background: cssObj.bodyCardMobile.input_about_card.background,
                                                border: cssObj.bodyCardMobile.input_about_card.border,
                                                outline: cssObj.bodyCardMobile.input_about_card.outline,
                                                opacity: cssObj.bodyCardMobile.input_about_card.opacity,
                                                borderBottom: cssObj.bodyCardMobile.input_about_card.borderBottom,
                                                margin: cssObj.bodyCardMobile.input_about_card.margin,
                                            }}
                                            expanded={card.expanded === 'reveiws'}
                                            onChange={this.handleChange('reveiws')}
                                            onClick={(e) => { e.stopPropagation(); setReveiwsToggle(); }}
                                        >
                                            <AccordionSummary
                                                className={cssObj.bodyCardMobile.styleTitleBody.textAlign  === "right" ? "direction" : ""}
                                                expandIcon={
                                                    <ExpandMoreIcon
                                                        style={{ color: cssObj.bodyCardMobile.styleTitleBody.color }}
                                                    />
                                                }
                                                aria-controls="reveiws-content"
                                                id="reveiwsbh-header"
                                            >
                                                <Typography>
                                                    {
                                                        showState ? reveiw.titleReveiws !== "" ?
                                                            reveiw.titleReveiws : "Reveiws" :
                                                            <input
                                                                style={cssObj.bodyCardMobile.styleTitleBody}
                                                                className={cssObj.cssObjTamplate.input_about_templet}
                                                                onChange={(e) => { setReveiwsTitle(e.target.value) }}
                                                                title={showState ? "" : "Change Reveiws title"}
                                                                value={reveiw.titleReveiws !== "" ? reveiw.titleReveiws : ""}
                                                                placeholder="Reveiws"
                                                                id={showState ? "about_p" : "about"}
                                                            />
                                                    }
                                                </Typography>
                                            </AccordionSummary>

                                            <AccordionDetails
                                                className="rounded"
                                                style={{ background: "transperent", color: "black" }}
                                            >
                                                <Typography
                                                    component={'span'}
                                                    style={{ width: "100%" }}
                                                >
                                                    <Reveiws {...this.props}></Reveiws>
                                                </Typography>
                                            </AccordionDetails>
                                        </Accordion> : ""
                            }
                            {/* video */}

                            {
                                video.videoShow ?
                                    showState && video.videoCard.url === ""
                                        && video.codeHtmlIframe === "" ? "" :
                                        <Accordion
                                            style={{
                                                letterSpacing: cssObj.bodyCardMobile.input_about_card.letterSpacing,
                                                color: cssObj.bodyCardMobile.styleTitleBody.color,
                                                background: cssObj.bodyCardMobile.input_about_card.background,
                                                border: cssObj.bodyCardMobile.input_about_card.border,
                                                outline: cssObj.bodyCardMobile.input_about_card.outline,
                                                opacity: cssObj.bodyCardMobile.input_about_card.opacity,
                                                borderBottom: cssObj.bodyCardMobile.input_about_card.borderBottom,
                                                margin: cssObj.bodyCardMobile.input_about_card.margin,
                                            }}
                                            expanded={card.expanded === 'video'}
                                            onChange={this.handleChange('video')}
                                            onClick={(e) => { e.stopPropagation(); setvideoStyleToggle(); }}
                                        >
                                            <AccordionSummary
                                                className={cssObj.bodyCardMobile.styleTitleBody.textAlign === "left" ? "" : "direction"}
                                                expandIcon={
                                                    <ExpandMoreIcon
                                                        style={{ color: cssObj.bodyCardMobile.styleTitleBody.color }}
                                                    />
                                                }
                                                aria-controls="reveiws-content"
                                                id="reveiwsbh-header"
                                            >
                                                <Typography>
                                                    {
                                                        showState ? video.titleVideo !== "" ?
                                                            video.titleVideo : "Video" :
                                                            <input
                                                                style={cssObj.bodyCardMobile.styleTitleBody}
                                                                className={cssObj.cssObjTamplate.input_about_templet}
                                                                onChange={(e) => { setVideoTitle(e.target.value) }}
                                                                title={showState ? "" : "Change Video title"}
                                                                value={video.titleVideo !== "" ? video.titleVideo : "Video"}
                                                                placeholder="video"
                                                                id={showState ? "about_p" : "about"}
                                                            />
                                                    }
                                                </Typography>
                                            </AccordionSummary>

                                            <AccordionDetails
                                                className="rounded"
                                                style={{ background: "transperent", color: "black" }}
                                            >
                                                <Typography
                                                    component={'span'}
                                                    style={{ width: "100%" }}
                                                >
                                                    <VideoCard></VideoCard>
                                                </Typography>
                                            </AccordionDetails>
                                        </Accordion> : ""
                            }

                        </div>
                        <div style={{ height: "50px" }}></div>
                        {card.currentCard.vCardStyle.footerShow ?

                            <div
                                className="row  d-flex justify-content-center email-form-padding-mobile"
                                id="DivIdToScroll"
                                style={
                                    cssObj ? cssObj.bodyCardMobile.background_footer ?
                                    cssObj.bodyCardMobile.background_footer : null : null
                                }
                                onClick={(e) => { e.stopPropagation(); setFooterStyleToggle(); }}
                            >
                                {
                                    card.currentCard.vCardStyle.titleFooterShow ?
                                        <div className=" row mt-4 d-flex justify-content-center">
                                            {
                                                !showState || !preview ?
                                                    <input
                                                        className="d-flex justify-content-center rounded border-0 "
                                                        placeholder=""
                                                        value={
                                                            cssObj.bodyCardMobile.footerMobile.titleFooter !== "" ?
                                                                cssObj.bodyCardMobile.footerMobile.titleFooter : ""
                                                        }
                                                        onChange={(e) => this.props.changeTitleFooterMobile(e.target.value)}
                                                        style={
                                                            cssObj ? cssObj.bodyCardMobile.input_footer_title ?
                                                                cssObj.bodyCardMobile.input_footer_title : null : null
                                                        }
                                                    /> :
                                                    <label
                                                        className="d-flex justify-content-center rounded border-0 "
                                                        style={
                                                            cssObj ? cssObj.bodyCardMobile.input_footer_title ?
                                                                cssObj.bodyCardMobile.input_footer_title : null : null
                                                        }
                                                    >
                                                        {
                                                            cssObj.bodyCardMobile.footerMobile.titleFooter !== "" ?
                                                                cssObj.bodyCardMobile.footerMobile.titleFooter : ""
                                                        }
                                                    </label>
                                            }
                                        </div> : ""
                                }

                                {
                                    card.currentCard.vCardStyle.fullNameFooterShow ?
                                        <input
                                            autoComplete="new-password"
                                            id="fellowName"
                                            type="text"
                                            className="rounded border-0 row mt-2 p-3"
                                            style={
                                                cssObj ? cssObj.bodyCardMobile.input_footer_background ?
                                                    cssObj.bodyCardMobile.input_footer_background : null : null
                                            }
                                            placeholder="Full name"
                                            value={
                                                card.currentCard.fullNameFooter !== "" ?
                                                    card.currentCard.fullNameFooter : ""
                                            }
                                            onChange={(e) => this.props.changeFullNameFooterMobile(e.target.value)}
                                        /> : ""
                                }
                                {
                                    card.currentCard.vCardStyle.personalEmailFooterShow ?
                                        <input
                                            autoComplete="new-password"
                                            id="fellowEmail" className="rounded border-0 row mt-2 p-3"
                                            style={
                                                cssObj ? cssObj.bodyCardMobile.input_footer_background ?
                                                    cssObj.bodyCardMobile.input_footer_background : null : null
                                            }
                                            placeholder="Email"
                                            value={
                                                card.currentCard.personalEmailFooter !== "" ?
                                                    card.currentCard.personalEmailFooter : ""
                                            }
                                            onChange={(e) => this.props.changePersonalEmailFooterMobile(e.target.value)}
                                            type="email"
                                        // onClick={setFooterStyleToggle}
                                        /> : ""
                                }
                                {
                                    card.currentCard.vCardStyle.mobileFooterShow ?
                                        <input
                                            autoComplete="new-password"
                                            id="fellowMobile" className="rounded border-0 row mt-2 p-3"
                                            style={
                                                cssObj ? cssObj.bodyCardMobile.input_footer_background ?
                                                    cssObj.bodyCardMobile.input_footer_background : null : null
                                            }
                                            placeholder="Mobile"
                                            value={
                                                card.currentCard.mobileFooter !== "" ?
                                                    card.currentCard.mobileFooter : ""
                                            }
                                            onChange={(e) => this.props.changeMobileFooterMobile(e.target.value)}
                                            type="tel"
                                        // onClick={setFooterStyleToggle}
                                        /> : ""
                                }
                                {
                                    card.currentCard.vCardStyle.messageFooterShow ?
                                        <div
                                            className="rounded border-0 row mt-2 d-flex justify-content-center"
                                            style={{ width: "92%" }}
                                        >
                                            <textarea
                                                id="massegeBody"
                                                className="rounded row p-3"
                                                style={
                                                    cssObj ? cssObj.bodyCardMobile.message_footer_background ?
                                                        cssObj.bodyCardMobile.message_footer_background : null : null
                                                }
                                                value={
                                                    card.currentCard.messageFooter !== "" ?
                                                        card.currentCard.messageFooter : ""
                                                }
                                                onChange={(e) => this.props.changeMessageFooterMobile(e.target.value)}
                                                // onClick={setFooterStyleToggle}
                                                placeholder="Message..."
                                            ></textarea>
                                        </div> : ""
                                }
                                <div
                                    className="row rounded d-flex justify-content-around mt-2"
                                    style={{ width: "92%" }}
                                >
                                    <div
                                        className="d-flex justify-content-center rounded mb-2"
                                        onClick={showState || preview ? this.props.sendEmail : null}
                                        style={
                                            cssObj ? cssObj.bodyCardMobile.button_footer ?
                                                cssObj.bodyCardMobile.button_footer : null : null
                                        }
                                    >
                                        {
                                            !showState && !preview ?
                                                <input
                                                    autoComplete="new-password"
                                                    className="rounded border-0"
                                                    type="text"
                                                    hidden={!card.currentCard.vCardStyle.buttonFooterShow}
                                                    value={
                                                        cssObj.bodyCardMobile.footerMobile.buttonFooter !== "" ?
                                                            cssObj.bodyCardMobile.footerMobile.buttonFooter : ""
                                                    }
                                                    onChange={(e) => this.props.changeButtonFooterMobile(e.target.value)}
                                                    style={
                                                        cssObj ? cssObj.bodyCardMobile.input_footer_button ?
                                                            cssObj.bodyCardMobile.input_footer_button : null : null
                                                    }
                                                /> :
                                                <label
                                                    className="rounded border-0"
                                                    style={
                                                        cssObj ? cssObj.bodyCardMobile.input_footer_button ?
                                                            cssObj.bodyCardMobile.input_footer_button : null : null
                                                    }
                                                >
                                                    {
                                                        cssObj.bodyCardMobile.footerMobile.buttonFooter !== "" ?
                                                            cssObj.bodyCardMobile.footerMobile.buttonFooter : ""
                                                    }
                                                </label>
                                        }

                                    </div>

                                </div>
                            </div>
                            : ""
                        }

                        {
                            !user.isPaying ?
                                <div 
                                    className={
                                        card.currentCard.vCardStyle.footerShow ?
                                        "signature-dark":"signature-light"
                                    }
                                >
                                    <span> Made With </span>
                                    <FontAwesomeIcon
                                        className="heart-icon"
                                        icon={['fas', 'heart']}
                                        size="xs"
                                    />&nbsp;
                                    <span>
                                        
                                        
                                        by &nbsp;
                                        <a 
                                            href="https://knowme.page/"
                                            className={
                                                card.currentCard.vCardStyle.footerShow ?
                                                "signature-text-dark":"signature-text-light"
                                            }
                                        >
                                            Knowme.page
                                        </a>
                                    </span>
                                </div> : ""
                        }

                    </div>
                </div>
            </>
        );
    }
}

export default BodyMpbile;