
function convertActionTypeToName(actionType) {
    return actionType.toLowerCase().replace(/_(\w)/g, v => v[1].toUpperCase());
  }
  
export default function createReducer(state, action, handlers) {
    const key = convertActionTypeToName(action.type);
    const handler = handlers[key];
    if (handler) {
        handler(state, action);
    }
}


export function getCardById(cards, cardId)
{
    const card = cards.find(card => card.id === cardId);
    return card;
}

export function getIndexCardById(cards, cardId)
{
  
    const index = cards.findIndex(card => card.id === cardId);
    return index;
}

export function getAgentByCardId(agents, agentId)
{
    const agent = agents.find(agent => agent.id === agentId);
    return agent;
}

export function getIndexSocialMedia(socialMedias,socialMediaName)
{
    const index = socialMedias.findIndex(event => event.name === socialMediaName);
    return index;
}

// statistics
export function sumData(data){

    // debugger;
    // בדיקה האם המערך שהגיע ריק
    if(data.length>0){
          if(data[0].sumContactOptions){
              let sumConcat=[];
              sumConcat=data.map(contactOption => JSON.parse(contactOption.sumContactOptions))
              return sumConcat.reduce(function(prev, contactOption) {
              let sum= Object.keys(contactOption).reduce((sum,key)=>sum+parseFloat(contactOption[key]||0),0);
              console.log("prev+sum"+prev+"sssssss"+sum)
              return prev + sum;
        },0);

    }

    // עבור viewers ו- submitioms
    else{
    return data.reduce(function(sum, obj) {
        return sum + obj.amount;
    },0);
   }}
   return 0;
}