import React from "react";
import { Switch } from "react-router-dom";
import { connect } from 'react-redux';
import { actions } from '../Redux/actions/Action';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Collapse } from 'reactstrap';
import ReveiwsNav from './Reveiws/ReveiwsNav';
import AgentDetails from './AgentDetails'
import GalleryNav from '../components/Gallery/GalleryNav'
import SocialMedia from './SocialMedia'
import VcardStyle from './VcardStyle'
import Footer from './footer'
import VideoNav from '../components/Video/VideoNav'

import '../Css/Nav.css'


function mapStateToProps(state) {
    return {
        collapse: state.card.collaps,
        userName: state.user.userName,
        gallery: state.gallerySlide.currentGallery,
        tamplate1: state.tamplate.tamplate2,



    };
}
function mapDispatchToProps(dispatch) {
    return {
        setAgentDetailsToggle: () => dispatch(actions.setAgentDetailsToggle()),
        setSocialMediaToggle: () => dispatch(actions.setSocialMediaToggle()),
        setvCardStyleToggle: () => dispatch(actions.setvCardStyleToggle()),
        setFooterStyleToggle: () => dispatch(actions.setFooterStyleToggle()),
        setGalleryStyleToggle: () => dispatch(actions.setGalleryStyleToggle()),
        setTamplateStyleToggle: () => dispatch(actions.setTamplateStyleToggle()),
        setvideoStyleToggle: () => dispatch(actions.setvideoStyleToggle()),
        setExpanded: (expanded) => dispatch(actions.setExpanded(expanded)),
        setReveiwsToggle: () => dispatch(actions.setReveiwsToggle()),
        setImgModalShow: (status) => dispatch(actions.setImgModalShow(status)),
        setImgId: (imgId) => dispatch(actions.setImgId(imgId)),
        setFunc: (func) => dispatch(actions.setFunc(func)),
        setOpenAllTabsUnConfigurator: (status) => dispatch(actions.setOpenAllTabsUnConfigurator(status))
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(function Nav(props) {
    const { collapse, setExpanded,
        setAgentDetailsToggle,
        setSocialMediaToggle, setReveiwsToggle, setvCardStyleToggle,
        setFooterStyleToggle, setGalleryStyleToggle, setvideoStyleToggle } = props;
    return (
        <div className="container-fluid mt-3 navbar-expand-lg " id="nav">
            <div className="row">
                <div className="col container w-100">

                    {/* vcardStyle */}

                    {/* <Link to={`${match.url}/vCardStyle`}> */}
                    <div className="row mb-2 btn_Details" onClick={setvCardStyleToggle} >
                        <button className="col container btn_Details_container"
                            style={{
                                border: collapse.vCardStyleOpen ? "none" : "",
                                color: collapse.vCardStyleOpen ? "#ddb6e4" : "#ffff"
                            }} >
                            <div className="row d-flex align-items-center">
                                <label className="col-9 mb-0 p-1">Card Settings</label>
                                <div className="col d-flex justify-content-end align-items-center p-1">
                                    {!collapse.vCardStyleOpen ? <FontAwesomeIcon icon={['fas', 'caret-right']}></FontAwesomeIcon> :
                                        <FontAwesomeIcon icon={['fas', 'sort-down']}></FontAwesomeIcon>}
                                </div>
                            </div>
                        </button>
                    </div>
                    {
                        <Collapse isOpen={collapse.vCardStyleOpen}>
                            <div className="row">
                                <VcardStyle></VcardStyle>
                            </div>
                        </Collapse>
                    }


                    {/* Agent Detail */}
                    {/* <Link to={`${match.url}/agentDetails`}> */}
                    <div    
                        className="row mb-2 btn_Details"
                        onClick={setAgentDetailsToggle}
                    >
                        <button className="col container btn_Details_container"
                            style={{
                                border: collapse.agentDetailsOpen ? "none" : "",
                                color: collapse.agentDetailsOpen ? "#ddb6e4" : "#ffff"

                            }}>                          
                              <div className="row d-flex align-items-center">
                                <label className="col-9 mb-0 p-1" >Your Details</label>

                                <div className="col d-flex justify-content-end align-items-center p-1">
                                    {!collapse.agentDetailsOpen ? <FontAwesomeIcon icon={['fas', 'caret-right']}></FontAwesomeIcon> :
                                        <FontAwesomeIcon icon={['fas', 'sort-down']}></FontAwesomeIcon>}
                                </div>
                            </div>
                        </button>
                    </div>
                    {<Collapse isOpen={collapse.agentDetailsOpen}>
                        <div className="row">
                            < AgentDetails></AgentDetails>
                        </div>
                    </Collapse>

                    }
                    {/* </Link> */}

                    {/* Social Media */}

                    {/* <Link to={`${match.url}/socialMedia`}> */}

                    <div className="row mb-2 rounded btn_Details" onClick={setSocialMediaToggle}>
                        <button className="col container btn_Details_container"
                            style={{
                                border: collapse.socialMediaOpen ? "none" : "",
                                color: collapse.socialMediaOpen ? "#ddb6e4" : "#ffff"

                            }}>
                            <div className="row d-flex align-items-center">
                                <label className="col-9 mb-0 p-1">Your Links</label>
                                <div className="col d-flex justify-content-end align-items-center p-1" >
                                    {!collapse.socialMediaOpen ? <FontAwesomeIcon icon={['fas', 'caret-right']}></FontAwesomeIcon> :
                                        <FontAwesomeIcon icon={['fas', 'sort-down']}></FontAwesomeIcon>}
                                </div>
                            </div>
                        </button>
                    </div>
                    {/* </Link> */}



                    {
                        <Collapse isOpen={collapse.socialMediaOpen}>
                            <div className="row">
                                <SocialMedia></SocialMedia>
                            </div>
                        </Collapse>
                    }

                  

                    <div className="row mb-2 btn_Details" onClick={setFooterStyleToggle} >
                        <button className="col container btn_Details_container"
                            style={{
                                border: collapse.footerStyleOpen ? "none" : "",
                                color: collapse.footerStyleOpen ? "#ddb6e4" : "#ffff"
                            }}>

                            <div className="row d-flex align-items-center">
                                <label className="col-9 mb-0 p-1">Footer Settings</label>
                                <div className="col d-flex justify-content-end align-items-center p-1">
                                    {!collapse.footerStyleOpen ? <FontAwesomeIcon icon={['fas', 'caret-right']}></FontAwesomeIcon> :
                                        <FontAwesomeIcon icon={['fas', 'sort-down']}></FontAwesomeIcon>}
                                </div>
                            </div>
                        </button>
                    </div>
                    {
                        <Collapse isOpen={collapse.footerStyleOpen}>
                            <div className="row">


                                <Footer></Footer>

                            </div>
                        </Collapse>
                    }

                    <div className="row mb-2 btn_Details" onClick={() => {
                        setGalleryStyleToggle();
                        setExpanded({ isExpanded: !collapse.galleryStyleOpen, panel: 'gallery' });
                    }} >
                        <button className="col container btn_Details_container" style={{
                            border: collapse.galleryStyleOpen ? "none" : "",
                            color: collapse.galleryStyleOpen ? "#ddb6e4" : "#ffff"
                        }}>
                            <div className="row d-flex align-items-center">
                                <label className="col-9 mb-0 p-1">Gallery Settings</label>
                                <div className="col d-flex justify-content-end align-items-center p-1">
                                    {!collapse.galleryStyleOpen ? <FontAwesomeIcon icon={['fas', 'caret-right']}></FontAwesomeIcon> :
                                        <FontAwesomeIcon icon={['fas', 'sort-down']}></FontAwesomeIcon>}
                                </div>
                            </div>
                        </button>
                    </div>
                    {
                        <Collapse isOpen={collapse.galleryStyleOpen}>
                            <div className="row">
                                <GalleryNav></GalleryNav>
                            </div>
                        </Collapse>
                    }

                    <div className="row mb-2 btn_Details" onClick={() => {
                        setReveiwsToggle();
                        setExpanded({ isExpanded: !collapse.reveiwsOpen, panel: 'reveiws' });
                    }} >
                        <button className="col container btn_Details_container" style={{
                            border: collapse.reveiwsOpen ? "none" : "",
                            color: collapse.reveiwsOpen ? "#ddb6e4" : "#ffff"

                        }}>
                            <div className="row d-flex align-items-center">
                                <label className="col-9 mb-0 p-1">Reveiws Settings</label>
                                <div className="col d-flex justify-content-end align-items-center p-1">
                                    {!collapse.reveiwsOpen ? <FontAwesomeIcon icon={['fas', 'caret-right']}></FontAwesomeIcon> :
                                        <FontAwesomeIcon icon={['fas', 'sort-down']}></FontAwesomeIcon>}
                                </div>
                            </div>
                        </button>
                    </div>
                    {
                        <Collapse isOpen={collapse.reveiwsOpen}>
                            <div className="row">
                                <ReveiwsNav {...props}></ReveiwsNav>
                            </div>
                        </Collapse>
                    }

                    <div className="row mb-2 btn_Details" onClick={() => {
                        setvideoStyleToggle();
                        setExpanded({ isExpanded: !collapse.videoStyleOpen, panel: 'video' });
                    }} >
                        <button className="col container btn_Details_container" style={{ border: collapse.videoStyleOpen ? "none" : "" }}>
                            <div className="row d-flex align-items-center">
                                <label className="col-9 mb-0 p-1">Video Style</label>
                                <div className="col d-flex justify-content-end align-items-center p-1">
                                    {!collapse.videoStyleOpen ? <FontAwesomeIcon icon={['fas', 'caret-right']}></FontAwesomeIcon> :
                                        <FontAwesomeIcon icon={['fas', 'sort-down']}></FontAwesomeIcon>}
                                </div>
                            </div>
                        </button>
                    </div>
                    {
                        <Collapse isOpen={collapse.videoStyleOpen}>
                            <div className="row">
                                <VideoNav {...props}></VideoNav>
                            </div>
                        </Collapse>
                    }

                    {/* tamplate */}

                    {/* <div className="row mb-2 btn_Details" onClick={setTamplateStyleToggle} >
                        <button className="col container btn_Details_container" style={{border:collapse.TamplateStyleOpen?"none":""}}>
                            <div className="row d-flex align-items-center">
                                <label className="col-9 mb-0 p-1">Templates</label>
                                <div className="col d-flex justify-content-end align-items-center p-1">
                                    {!collapse.TamplateStyleOpen ? <FontAwesomeIcon icon={['fas', 'caret-right']}></FontAwesomeIcon> :
                                        <FontAwesomeIcon icon={['fas', 'sort-down']}></FontAwesomeIcon>}
                                </div>
                            </div>
                        </button>
                    </div>
                    {
                        <Collapse isOpen={collapse.TamplateStyleOpen}>

                            <div className="row">
                                <Link to={`/${userName}/Tamplates`}>iiii</Link>
                              </div>
                        </Collapse>
                    }  


                    {/* </Link> */}
                    <Switch>

                        {/* <Route path={`${match.path}/agentDetails`}>
                            <div className="row">
                                < AgentDetails {...allChildProps}></AgentDetails>
                            </div>
                        </Route> */}

                        {/* <Route path={`${match.path}/socialMedia`}>
                            <div className="row">
                                <SocialMedia></SocialMedia>
                            </div>
                        </Route> */}

                        {/* <Route path={`${match.path}/vCardStyle`}>
                            <div className="row">
                                <VcardStyle></VcardStyle>
                            </div>
                        </Route> */}
                    </Switch>
                </div>

            </div>
            <div style={{height:"20px"}}></div>
            {/* <button style={{
                width: "50px",
                height: "50px"
            }} onClick={() => setVcardStyle(tamplate1)}>Style</button> */}
        </div >
        

    );
});
