import produce from 'immer';
import createReducer from "./reducerUtils";


const initialState = {
    alertStatuseDelete: false,
    alertStatusePublish: false,
    alertStatuseSucsses: false,
    alertStatuseUpLodeImge: false,
    alertLink:false,
    alertStatusePreview: false
};

const alert = {

    setalertStatuseDelete(state, action) {
        state.alertStatuseDelete = action.payload;
    },
    setalertLink(state, action) {
        state.alertLink = !state.alertLink;
    },
    setalertStatusePublish(state, action) {
        state.alertStatusePublish = action.payload;
    },
    setalertStatuseSucsses(state, action) {
        state.alertStatuseSucsses = action.payload;
    },

    setalertStatuseUpLodeImge(state, action) {
        state.alertStatuseUpLodeImge = action.payload;
    },
    setalertStatusePreview(state, action) {
        state.alertStatusePreview = action.payload;
    },
}

export default produce((state, action) => createReducer(state, action, alert), initialState);